/**
 *
 * CbcrudGrid
 * This component should be used for any ClearBlade entities that can use the Query object (collections, devices, users, edges, etc.)
 */

import React from 'react';
import styled from 'styled-components';
import { FormattedMessage } from 'react-intl';

import { Button, ButtonGroup } from 'reactstrap';
import { SortingRule, Column } from 'react-table';
import { TableColumn } from 'containers/Console/state';
import FilterModal from 'components/FilterModal';
import CreateItemModal from 'components/CreateItemModal';
import { cb } from 'cb-utils/cbLib';
import { Condition } from 'cb-utils/console-entity-models/FilterCondition';
import CRUDGrid, { Row } from 'components/CrudGrid';
import { downloadCSV } from 'components/CrudGridContainer/utils';
import {
  RefreshIcon,
  DownloadIcon,
  PlusIcon,
  MinusIcon,
  FilterIcon,
} from 'components/Icons';
import messages from './messages';
import { AnyMap, PrimitiveType } from 'cb-utils/console-entity-models';

const MyWrapper = styled.div`
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
`;

const ButtonRow = styled.div`
  display: flex;
  margin: 0 0 0.5rem 0;
  flex-shrink: 0;
`;

const addSortToQuery = (sortObj: SortingRule, query: QueryObj) => {
  if (query.query.SORT && query.query.SORT.length > 0) {
    query.query.SORT = [];
  }
  if (sortObj.desc) {
    query.descending(sortObj.id);
  } else {
    query.ascending(sortObj.id);
  }
};

export interface CbcrudGridProps {
  rows: object[];
  columns: TableColumn[];
  columnNames?: string[];
  count: number;
  refresh: (q: QueryObj) => void;
  isLoading: boolean;
  isDeleting?: boolean;
  onFetchData: (q: QueryObj) => void;
  pkColumn: string;
  transformColumns?: (columns: Column[]) => Column[];
  addEventCallback?: (r: Row) => void;
  deleteEventCallback?: (rows: string[]) => void;
  updateEventCallback?: (
    row: AnyMap,
    col: string,
    value: PrimitiveType,
  ) => void;
  editColumnsCallback?: () => void;
  exportCallback?: () => void;
  allowSelect?: boolean;
  allowFilter?: boolean;
  allowSort?: boolean;
  allowExport?: boolean;
  showCreateModal?: boolean;
  addItemSuccess?: boolean;
  topRow?: JSX.Element;
  resetCallbackStatus?: () => void;
  onSelectRow?: (rows: string[]) => void;
  selectedItems?: string[];
  pageSize?: number;
  disablePaginationOptions?: boolean;
}

interface CbcrudGridState {
  isFilterModalOpen: boolean;
  isCreateItemModalOpen: boolean;
  selected: string[];
}

class CbcrudGrid extends React.PureComponent<CbcrudGridProps, CbcrudGridState> {
  // eslint-disable-line react/prefer-stateless-function

  static defaultProps: Partial<CbcrudGridProps> = {
    rows: [],
    columns: [],
    isLoading: false,
    count: 0,
    transformColumns: null,
    allowFilter: false,
    deleteEventCallback: null,
    addEventCallback: null,
    showCreateModal: true,
    disablePaginationOptions: false,
  };

  state: CbcrudGridState = {
    isFilterModalOpen: false,
    isCreateItemModalOpen: false,
    selected: [],
  };

  condition: Condition = null;
  query: QueryObj = cb.Query();
  defaultPageSize = 25;

  render() {
    const {
      rows,
      columns,
      refresh,
      isLoading,
      pkColumn,
      transformColumns,
      addEventCallback,
      updateEventCallback,
      deleteEventCallback,
      editColumnsCallback,
      allowSelect,
      allowFilter,
      allowSort,
      allowExport,
      count,
    } = this.props;
    const { isFilterModalOpen } = this.state;
    return (
      <MyWrapper
        data-cy={
          isLoading ? 'crud-grid-container-loading' : 'crud-grid-container'
        }
      >
        <ButtonRow>
          <ButtonGroup size="sm">
            {addEventCallback && this._createInsertButton()}
            {deleteEventCallback && this._createDeleteButton()}
          </ButtonGroup>
          {this.props.topRow}
          {editColumnsCallback && this._createEditColumnsButton()}
          <ButtonGroup size="sm" className="ml-auto">
            {allowFilter && (
              <Button onClick={() => this._toggleFilterModal(true)}>
                <FilterIcon />
              </Button>
            )}
            {(allowExport || this.props.exportCallback) &&
              this._createExportButton()}
            <Button onClick={() => refresh(this.query)}>
              <RefreshIcon />
            </Button>
          </ButtonGroup>
        </ButtonRow>
        {isFilterModalOpen && (
          <FilterModal
            condition={this._getCondition()}
            onDismiss={() => this._toggleFilterModal(false)}
            onApply={this._onFilter}
            columns={columns}
          />
        )}
        <CRUDGrid
          data={rows}
          loading={isLoading}
          keyColumn={pkColumn}
          columns={columns}
          noUpdateColumns={this.getUneditableColumns(columns)}
          enableSorting={allowSort}
          selected={
            this.props.selectedItems
              ? this.props.selectedItems
              : this.state.selected
          }
          selectEventCallback={
            this.props.onSelectRow ? this.props.onSelectRow : this.onSelect
          }
          showPagination
          disablePaginationOptions={this.props.disablePaginationOptions}
          pageSizeOptions={[25, 50, 100]}
          defaultPageSize={this.defaultPageSize}
          pageSize={this.props.pageSize}
          count={count}
          updateEventCallback={updateEventCallback}
          allowSelect={allowSelect}
          isDeleting={this.props.isDeleting}
          columnNames={this.props.columnNames}
          onFetchData={(state) => {
            this.query.setPage(
              state.pageSize !== undefined
                ? state.pageSize
                : state.defaultPageSize,
              state.page + 1, // pages are 0-indexed by react-table but our backend is 1-indexed
            );
            if (state.sorted && state.sorted.length > 0) {
              addSortToQuery(state.sorted[0], this.query);
            }
            this.props.onFetchData(this.query);
          }}
          transformColumns={transformColumns}
        />
        {this.state.isCreateItemModalOpen && (
          <CreateItemModal
            columns={columns}
            addEventCallback={addEventCallback}
            onDismiss={() => this._toggleCreateItemModal(false)}
            addItemSuccess={this.props.addItemSuccess}
            resetCallbackStatus={this.props.resetCallbackStatus}
          />
        )}
      </MyWrapper>
    );
  }

  _createInsertButton = () => {
    return (
      <Button color="secondary" onClick={this._insertRow}>
        <PlusIcon />
        <FormattedMessage {...messages.addRow} />
      </Button>
    );
  };

  _insertRow = () => {
    if (this.props.showCreateModal) {
      this._toggleCreateItemModal(true);
    } else {
      this.props.addEventCallback({ data: {} });
    }
  };

  _toggleCreateItemModal = (shouldShow: boolean) => {
    this.setState({
      ...this.state,
      isCreateItemModalOpen: shouldShow,
    });
  };

  _createDeleteButton = () => {
    return (
      <Button
        color="secondary"
        onClick={this._onDelete}
        disabled={this.state.selected.length === 0}
        data-cy="delete-rows"
      >
        <MinusIcon />
        <FormattedMessage {...messages.deleteRows} />
      </Button>
    );
  };

  _createEditColumnsButton = () => {
    return (
      <Button
        className="ml-2"
        data-cy="edit-columns"
        size="sm"
        onClick={this.props.editColumnsCallback}
      >
        <FormattedMessage {...messages.editColumns} />
      </Button>
    );
  };

  onSelect = (ids: string[]) => {
    this.setState({
      ...this.state,
      selected: ids,
    });
  };

  _onDelete = () => {
    this.props.deleteEventCallback(this.state.selected);
    this.setState({
      ...this.state,
      selected: [],
    });
  };

  _createExportButton = () => {
    const { rows, exportCallback } = this.props;
    if (rows) {
      return (
        <Button
          className="btn btn-secondary"
          onClick={() =>
            exportCallback ? exportCallback() : downloadCSV(rows)
          }
        >
          <DownloadIcon />
        </Button>
      );
    }
    return null;
  };

  _onFilter = (q: QueryObj, cond: Condition) => {
    this.condition = cond;
    this.query.query.FILTERS = q.query.FILTERS;
    this.props.onFetchData(this.query);
    this._toggleFilterModal(false);
  };

  _toggleFilterModal = (shouldShow: boolean) => {
    this.setState({
      ...this.state,
      isFilterModalOpen: shouldShow,
    });
  };

  _getCondition = () => {
    if (this.props.columns.length > 0 && this.condition === null) {
      for (let i = 0, len = this.props.columns.length; i < len; i += 1) {
        // what's going on here?
        // we're creating a default condition so that the modal opens up with
        // a pre-filled field
        this.condition = {
          id: 'fakeId',
          rules: [
            {
              id: 'anotherFakeId',
              field: this.props.columns[i].ColumnName,
              value: '',
              operator: '==',
            },
          ],
          combinator: 'and',
        };
        break;
      }
    }
    return JSON.parse(JSON.stringify(this.condition));
  };

  getUneditableColumns = (columns: TableColumn[]): string[] => {
    return columns
      .filter((column) => column.noUpdateAllowed)
      .map((col) => col.ColumnName);
  };
}

export default CbcrudGrid;



