import { createSelector } from 'reselect';
import { STORE_KEY } from './constants';
import { State } from './reducer';
import { AppState } from 'reducers';

/**
 * Direct selector to the adapterConfigModal state domain
 */
const selectAdapterConfigModalDomain = () => (state: AppState): State =>
  state[STORE_KEY];

/**
 * Other specific selectors
 */

const selectAdapterUploadFiles = () =>
  createSelector(
    makeSelectAdapterConfigModal(),
    (substate) => substate.uploadFiles,
  );

const selectAdapterReplaceFiles = () =>
  createSelector(
    makeSelectAdapterConfigModal(),
    (substate) => substate.replaceFiles,
  );

const selectModifiableAdapter = () =>
  createSelector(
    makeSelectAdapterConfigModal(),
    (substate) => substate.modifiableAdapter,
  );

const selectModifiableAdapterFiles = () =>
  createSelector(makeSelectAdapterConfigModal(), (substate) => substate.files);

const selectAdapterIsUpdating = () =>
  createSelector(
    makeSelectAdapterConfigModal(),
    (substate) => substate.isUpdating,
  );

const selectAdapterDeleteFiles = () =>
  createSelector(
    makeSelectAdapterConfigModal(),
    (substate) => substate.deleteFiles,
  );
/**
 * Default selector used by AdapterConfigModal
 */

const makeSelectAdapterConfigModal = () =>
  createSelector(selectAdapterConfigModalDomain(), (substate) => substate);

export default makeSelectAdapterConfigModal;
export {
  selectAdapterConfigModalDomain,
  selectAdapterUploadFiles,
  selectAdapterReplaceFiles,
  selectModifiableAdapter,
  selectModifiableAdapterFiles,
  selectAdapterIsUpdating,
  selectAdapterDeleteFiles,
};



