/*
 *
 * AdapterConfigModal actions
 *
 */

import { BaseAction } from 'cb-utils/baseAction';
import { ActionConstants } from './actionConstants';
import {
  AdapterModel,
  AdapterFileReplaceModel,
  AdapterFileUploadModel,
  AdapterFileModel,
} from 'cb-utils/console-entity-models';

export interface SetModalAdapterAndFiles extends BaseAction {
  type: ActionConstants.SET_MODAL_ADAPTER_AND_FILES;
  payload: {
    adapter: AdapterModel;
    files: AdapterFileModel[];
  };
}

export const createSetModalAdapterAndFiles = (
  adapter: AdapterModel,
  files: AdapterFileModel[],
): SetModalAdapterAndFiles => ({
  type: ActionConstants.SET_MODAL_ADAPTER_AND_FILES,
  payload: {
    adapter,
    files,
  },
});

export interface AddReplaceFile extends BaseAction {
  type: ActionConstants.ADD_REPLACE_FILE;
  payload: {
    file: AdapterFileReplaceModel | {};
    name: string;
  };
}

export const createAddReplaceFile = (
  file: AdapterFileReplaceModel | {},
  name: string,
): AddReplaceFile => ({
  type: ActionConstants.ADD_REPLACE_FILE,
  payload: {
    file,
    name,
  },
});

export interface AddNewFiles extends BaseAction {
  type: ActionConstants.ADD_NEW_FILES;
  payload: {
    files: AdapterFileUploadModel[] | any[];
  };
}

export const createAddNewFiles = (
  files: AdapterFileUploadModel[] | any[],
): AddNewFiles => ({
  type: ActionConstants.ADD_NEW_FILES,
  payload: {
    files,
  },
});

export interface UpdateCommand extends BaseAction {
  type: ActionConstants.UPDATE_COMMAND;
  payload: {
    adapter: AdapterModel;
  };
}

export const createUpdateCommand = (adapter: AdapterModel): UpdateCommand => ({
  type: ActionConstants.UPDATE_COMMAND,
  payload: {
    adapter,
  },
});

export interface DeleteFile extends BaseAction {
  type: ActionConstants.DELETE_FILE;
  payload: {
    name: string;
  };
}

export const createDeleteFile = (name: string): DeleteFile => ({
  type: ActionConstants.DELETE_FILE,
  payload: {
    name,
  },
});

export interface DeleteReplaceFile extends BaseAction {
  type: ActionConstants.DELETE_REPLACE_FILE;
  payload: {
    name: string;
  };
}

export const createDeleteReplaceFile = (name: string): DeleteReplaceFile => ({
  type: ActionConstants.DELETE_REPLACE_FILE,
  payload: {
    name,
  },
});

export interface DeleteNewFile extends BaseAction {
  type: ActionConstants.DELETE_NEW_FILE;
  payload: {
    name: string;
  };
}

export const createDeleteNewFile = (name: string): DeleteNewFile => ({
  type: ActionConstants.DELETE_NEW_FILE,
  payload: {
    name,
  },
});

export interface UpdateAdapterRequest extends BaseAction {
  type: ActionConstants.UPDATE_ADAPTER_REQUEST;
  payload: {
    sysKey: string;
    name: string;
  };
}

export const createUpdateAdapterRequest = (
  sysKey: string,
  name: string,
): UpdateAdapterRequest => ({
  type: ActionConstants.UPDATE_ADAPTER_REQUEST,
  payload: {
    sysKey,
    name,
  },
});

export interface ClearConfigModalInfo {
  type: ActionConstants.CLEAR_CONFIG_MODAL_INFO;
}

export const createClearConfigModalInfo = (): ClearConfigModalInfo => ({
  type: ActionConstants.CLEAR_CONFIG_MODAL_INFO,
});

export type ActionTypes =
  | AddReplaceFile
  | AddNewFiles
  | SetModalAdapterAndFiles
  | UpdateCommand
  | DeleteFile
  | DeleteNewFile
  | DeleteReplaceFile
  | UpdateAdapterRequest
  | ClearConfigModalInfo;



