import { all, takeEvery, call, put, select } from 'redux-saga/effects';
import { ActionConstants } from './actionConstants';
import {
  FetchEdgesForAdapterRequest,
  createFetchEdgesForAdapterSuccess,
  CallAdapterCommandRequest,
  createFetchEdgesForAdapterRequest,
  ShowLogModal,
  FetchLogsRequest,
  createFetchLogsSuccess,
} from './actions';
import { fetchAllEdgesQuery } from 'cb-utils/services/edges';
import { EdgeModel } from 'cb-utils/console-entity-models';
import {
  callAdapterCommandOnEdges,
  AdapterCommandResponse,
} from 'cb-utils/services/adapters';
import { makeSelectSelectedEdges } from 'containers/AdapterEdgeManager/selectors';
import { createNewNotification } from 'containers/Console/actions/ui';
import { edgeActions } from 'containers/Console/ducks';
import { getSystemKey } from 'cb-utils/getPathParams';

function* callFetchEdgesForAdapter(action: FetchEdgesForAdapterRequest) {
  try {
    const resp = yield call(fetchAllEdgesQuery, {
      query: action.payload.query,
    });
    const edges = resp.assets;
    const edgeNames = edges.map((edge: EdgeModel) => edge.name);
    const response: AdapterCommandResponse = yield call(
      callAdapterCommandOnEdges,
      action.payload.name,
      {
        command: 'status',
        edges: edgeNames,
      },
    );
    const runningEdgeNames = [];
    const stoppedEdgeNames = [];
    const disconnectedEdgeNames = [];
    // If none of these criteria are met, that means the edge is running and this adapter is not deployed to it.
    // The idea is that if you know the adapter is deployed to an edge, it should show up under either Running or Stopped
    // but if not, it should show up under Disconnected Edges because the edge is not running
    for (const [key, value] of Object.entries(response.result)) {
      if (value.output.replace(/[\n\r]/g, '') === 'Running') {
        runningEdgeNames.push(key);
      } else if (value.output.replace(/[\n\r]/g, '') === 'Stopped') {
        stoppedEdgeNames.push(key);
      } else if (
        value.error.includes('not in cache') ||
        value.error.includes('is not connected')
      ) {
        disconnectedEdgeNames.push(key);
      } else if (value.error.length > 0) {
        stoppedEdgeNames.push(key);
        yield put(
          createNewNotification({
            title: `Error running${action.payload.name} on ${key}: ${
              value.error
            }`,
            className: 'error',
          }),
        );
      }
    }
    const runningEdges = [];
    const stoppedEdges = [];
    const disconnectedEdges = [];
    for (const edge of edges) {
      if (runningEdgeNames.includes(edge.name)) {
        runningEdges.push(edge);
      } else if (stoppedEdgeNames.includes(edge.name)) {
        stoppedEdges.push(edge);
      } else if (disconnectedEdgeNames.includes(edge.name)) {
        disconnectedEdges.push(edge);
      }
    }
    yield put(edgeActions.fetchColumns.request({ systemKey: getSystemKey() }));
    yield put(
      createFetchEdgesForAdapterSuccess(
        runningEdges,
        stoppedEdges,
        disconnectedEdges,
      ),
    );
  } catch (e) {
    throw new Error(e);
  }
}

function* callCallAdapterCommand(action: CallAdapterCommandRequest) {
  const selectedEdges = yield select(makeSelectSelectedEdges());
  const response: AdapterCommandResponse = yield call(
    callAdapterCommandOnEdges,
    action.payload.name,
    {
      command: action.payload.command,
      edges: selectedEdges,
    },
  );
  for (const [key, value] of Object.entries(response.result)) {
    if (value.error.length > 0) {
      yield put(
        createNewNotification({
          title: `Error with request for ${key}: ${value.error}`,
          className: 'error',
        }),
      );
      yield put(
        createFetchEdgesForAdapterRequest(
          action.payload.name,
          action.payload.status,
        ),
      );
    } else {
      yield put(
        createNewNotification({
          title: `Command successfully called on ${key}`,
        }),
      );
      yield put(
        createFetchEdgesForAdapterRequest(
          action.payload.name,
          action.payload.status,
        ),
      );
    }
  }
}

function* callFetchAdapterLogs(action: ShowLogModal | FetchLogsRequest) {
  try {
    const response = yield call(
      callAdapterCommandOnEdges,
      action.payload.adapterName,
      {
        command: 'logs',
        edges: [action.payload.edgeName],
      },
    );
    const feedback =
      response.result[action.payload.edgeName].error.length > 0
        ? response.result[action.payload.edgeName].error
        : response.result[action.payload.edgeName].output;
    yield put(createFetchLogsSuccess(action.payload.edgeName, feedback));
  } catch (e) {
    throw new Error(e);
  }
}

export function* fetchEdgesForAdapter() {
  yield takeEvery(
    ActionConstants.FETCH_EDGES_FOR_ADAPTER_REQUEST,
    callFetchEdgesForAdapter,
  );
}

export function* callAdapterCommand() {
  yield takeEvery(
    ActionConstants.CALL_ADAPTER_COMMAND_REQUEST,
    callCallAdapterCommand,
  );
}

export function* fetchLogsForEdge() {
  yield takeEvery(ActionConstants.SHOW_LOG_MODAL, callFetchAdapterLogs);
}

export function* refreshLogs() {
  yield takeEvery(ActionConstants.FETCH_LOGS_REQUEST, callFetchAdapterLogs);
}

// All sagas to be loaded
export default function*() {
  yield all([
    fetchEdgesForAdapter(),
    callAdapterCommand(),
    fetchLogsForEdge(),
    refreshLogs(),
  ]);
}



