/*
 *
 * AdapterConfigModal reducer
 *
 */

import assign from 'immutable-assign';
import { ActionTypes } from './actions';
import { ActionConstants } from './actionConstants';
import {
  AdapterFileUploadModel,
  AdapterFileReplaceModel,
  AdapterModel,
  AdapterFileModel,
} from 'cb-utils/console-entity-models';

export interface State {
  modifiableAdapter: AdapterModel;
  files: AdapterFileModel[];
  uploadFiles: AdapterFileUploadModel[];
  replaceFiles: AdapterFileReplaceModel[];
  deleteFiles: string[];
  isUpdating: boolean;
}

export const initialState: State = {
  modifiableAdapter: null,
  files: [],
  uploadFiles: [],
  replaceFiles: [],
  deleteFiles: [],
  isUpdating: false,
};

function adapterConfigModalReducer(
  state: State = initialState,
  action: ActionTypes,
) {
  switch (action.type) {
    case ActionConstants.SET_MODAL_ADAPTER_AND_FILES:
      return assign(state, (m) => {
        m.modifiableAdapter = action.payload.adapter;
        m.files = [...action.payload.files];
        return m;
      });
    case ActionConstants.ADD_REPLACE_FILE:
      return assign(state, (m) => {
        const newFiles = m.files.filter(
          (file) => file.name !== action.payload.name,
        );
        m.files = [...newFiles];
        m.replaceFiles = [...m.replaceFiles, action.payload.file];
        return m;
      });
    case ActionConstants.ADD_NEW_FILES:
      return assign(
        state,
        (m) => m.uploadFiles,
        (f) => [...f, ...action.payload.files],
      );
    case ActionConstants.UPDATE_COMMAND:
      return assign(
        state,
        (m) => m.modifiableAdapter,
        () => action.payload.adapter,
      );
    case ActionConstants.DELETE_FILE:
      return assign(state, (s) => {
        // s.files, (a) => a.filter
        const newFiles = s.files.filter(
          (file) => file.name !== action.payload.name,
        );
        s.files = [...newFiles];
        s.deleteFiles = [...s.deleteFiles, action.payload.name];
        return s;
      });
    case ActionConstants.DELETE_NEW_FILE:
      return assign(state, (s) => {
        const newFiles = s.uploadFiles.filter(
          (file: AdapterFileUploadModel) => file.name !== action.payload.name,
        );
        s.uploadFiles = [...newFiles];
        return s;
      });
    case ActionConstants.DELETE_REPLACE_FILE:
      return assign(state, (s) => {
        const newFiles = s.replaceFiles.filter(
          (file: AdapterFileReplaceModel) => file.name !== action.payload.name,
        );
        s.replaceFiles = [...newFiles];
        return s;
      });
    case ActionConstants.UPDATE_ADAPTER_REQUEST:
      return assign(state, (s) => s.isUpdating, () => true);
    case ActionConstants.CLEAR_CONFIG_MODAL_INFO:
      return assign(state, (s) => {
        s.modifiableAdapter = null;
        s.files = [];
        s.uploadFiles = [];
        s.replaceFiles = [];
        s.deleteFiles = [];
        s.isUpdating = false;
        return s;
      });
    default:
      return state;
  }
}

export default adapterConfigModalReducer;



