import { TableColumn } from 'containers/Console/state';
import { ColumnModel, Map } from '.';

export const convertEntityMapToTableColumnList = (
  entities: Map<ColumnModel>,
): TableColumn[] => {
  const rtn = [];

  for (const key in entities) {
    if (entities.hasOwnProperty(key)) {
      rtn.push(entities[key]);
    }
  }

  return rtn;
};



