/*
 *
 * AdapterEdgeManager actions
 *
 */

import { BaseAction } from 'cb-utils/baseAction';
import { ActionConstants } from './actionConstants';
import { EdgeModel } from 'cb-utils/console-entity-models';

export interface DefaultAction extends BaseAction {
  type: ActionConstants.DEFAULT_ACTION;
}

export const createDefaultAction = (): DefaultAction => ({
  type: ActionConstants.DEFAULT_ACTION,
});

export interface SelectNewTab extends BaseAction {
  type: ActionConstants.SELECT_NEW_TAB;
  payload: {
    status: string;
  };
}

export const createSelectNewTab = (status: string): SelectNewTab => ({
  type: ActionConstants.SELECT_NEW_TAB,
  payload: {
    status,
  },
});

export interface ToggleEdgeItem extends BaseAction {
  type: ActionConstants.TOGGLE_EDGE_ITEM;
  payload: {
    selectedEdges: string[];
  };
}

export const createToggleEdgeItem = (
  selectedEdges: string[],
): ToggleEdgeItem => ({
  type: ActionConstants.TOGGLE_EDGE_ITEM,
  payload: {
    selectedEdges,
  },
});

export interface FetchEdgesForAdapterRequest extends BaseAction {
  type: ActionConstants.FETCH_EDGES_FOR_ADAPTER_REQUEST;
  payload: {
    name: string;
    status: string;
    query?: QueryObj;
  };
}

export const createFetchEdgesForAdapterRequest = (
  name: string,
  status: string,
  query?: QueryObj,
): FetchEdgesForAdapterRequest => ({
  type: ActionConstants.FETCH_EDGES_FOR_ADAPTER_REQUEST,
  payload: {
    name,
    status,
    query,
  },
});

export interface FetchEdgesForAdapterSuccess extends BaseAction {
  type: ActionConstants.FETCH_EDGES_FOR_ADAPTER_SUCCESS;
  payload: {
    runningEdges: EdgeModel[];
    stoppedEdges: EdgeModel[];
    disconnectedEdges: EdgeModel[];
  };
}

export const createFetchEdgesForAdapterSuccess = (
  runningEdges: EdgeModel[],
  stoppedEdges: EdgeModel[],
  disconnectedEdges: EdgeModel[],
): FetchEdgesForAdapterSuccess => ({
  type: ActionConstants.FETCH_EDGES_FOR_ADAPTER_SUCCESS,
  payload: {
    runningEdges,
    stoppedEdges,
    disconnectedEdges,
  },
});

export interface CallAdapterCommandRequest extends BaseAction {
  type: ActionConstants.CALL_ADAPTER_COMMAND_REQUEST;
  payload: {
    name: string;
    command: string;
    status: string;
  };
}

export const createCallAdapterCommandRequest = (
  name: string,
  command: string,
  status: string,
): CallAdapterCommandRequest => ({
  type: ActionConstants.CALL_ADAPTER_COMMAND_REQUEST,
  payload: {
    name,
    command,
    status,
  },
});

export interface ShowLogModal extends BaseAction {
  type: ActionConstants.SHOW_LOG_MODAL;
  payload: {
    adapterName: string;
    edgeName: string;
  };
}

export const createShowLogModal = (
  adapterName: string,
  edgeName: string,
): ShowLogModal => ({
  type: ActionConstants.SHOW_LOG_MODAL,
  payload: {
    adapterName,
    edgeName,
  },
});

export interface FetchLogsRequest extends BaseAction {
  type: ActionConstants.FETCH_LOGS_REQUEST;
  payload: {
    adapterName: string;
    edgeName: string;
  };
}

export const createFetchLogsRequest = (
  adapterName: string,
  edgeName: string,
): FetchLogsRequest => ({
  type: ActionConstants.FETCH_LOGS_REQUEST,
  payload: {
    adapterName,
    edgeName,
  },
});

export interface FetchLogsSuccess extends BaseAction {
  type: ActionConstants.FETCH_LOGS_SUCCESS;
  payload: {
    name: string;
    logs: string;
  };
}

export const createFetchLogsSuccess = (
  name: string,
  logs: string,
): FetchLogsSuccess => ({
  type: ActionConstants.FETCH_LOGS_SUCCESS,
  payload: {
    name,
    logs,
  },
});

export interface HideLogModal extends BaseAction {
  type: ActionConstants.HIDE_LOG_MODAL;
}

export const createHideLogModal = (): HideLogModal => ({
  type: ActionConstants.HIDE_LOG_MODAL,
});

export type ActionTypes =
  | DefaultAction
  | SelectNewTab
  | ToggleEdgeItem
  | FetchEdgesForAdapterRequest
  | FetchEdgesForAdapterSuccess
  | CallAdapterCommandRequest
  | ShowLogModal
  | HideLogModal
  | FetchLogsRequest
  | FetchLogsSuccess;



