import { all, takeEvery, put, select, call } from 'redux-saga/effects';
import { ActionConstants } from './actionConstants';
import {
  selectModifiableAdapter,
  selectAdapterUploadFiles,
  selectAdapterReplaceFiles,
  selectAdapterDeleteFiles,
} from 'containers/AdapterConfigModal/selectors';
import { UpdateAdapterRequest } from './actions';
import {
  updateAdapter,
  uploadAdapterFile,
  replaceAdapterFile,
  deleteAdapterFile,
} from 'cb-utils/services/adapters';
import {
  AdapterModel,
  AdapterFileUploadModel,
  AdapterFileReplaceModel,
} from 'cb-utils/console-entity-models';
import { createNewNotification } from 'containers/Console/actions/ui';
import {
  createDismissAdapterConfigModal,
  createFetchAdapterFiles,
} from 'containers/AdapterDetail/actions';
import { adapterActions } from '../Console/ducks';
import { selectAdapterByName } from 'containers/Console/selectors/adapters';

const adapterCommandsChanged = (
  modifiableAdapter: AdapterModel,
  originalAdapter: AdapterModel,
) => {
  for (const key in originalAdapter) {
    if (originalAdapter[key] !== modifiableAdapter[key]) {
      return true;
    }
  }
  return false;
};

function* callUpdateAllAdapterInfo(action: UpdateAdapterRequest) {
  const adapter: AdapterModel = yield select(selectModifiableAdapter());
  delete adapter.name;
  delete adapter.version;
  delete adapter.protocol;
  delete adapter.namespace;
  const uploadFiles: AdapterFileUploadModel[] = yield select(
    selectAdapterUploadFiles(),
  );
  const replaceFiles: AdapterFileReplaceModel[] = yield select(
    selectAdapterReplaceFiles(),
  );
  const deleteFiles: string[] = yield select(selectAdapterDeleteFiles());
  const name: string = action.payload.name;
  const sysKey: string = action.payload.sysKey;
  try {
    yield call(deleteAllFiles, sysKey, name, deleteFiles);
    const originalAdapter = yield select(selectAdapterByName(name));
    if (adapterCommandsChanged(adapter, originalAdapter)) {
      yield call(updateAdapter, sysKey, name, adapter);
    }
    yield call(uploadAllFiles, sysKey, name, uploadFiles);
    yield call(replaceAllFiles, sysKey, name, replaceFiles);
    yield put(adapterActions.fetchAll.request(sysKey));
    yield put(createFetchAdapterFiles(sysKey, name));
    yield put(createDismissAdapterConfigModal());
    yield put(
      createNewNotification({
        title: 'Adapter successfully updated',
      }),
    );
  } catch (e) {
    throw new Error(e);
  }
}

function* uploadAllFiles(
  sysKey: string,
  name: string,
  files: AdapterFileUploadModel[],
) {
  for (const file of files) {
    yield call(uploadAdapterFile, sysKey, name, file);
  }
}

function* replaceAllFiles(
  sysKey: string,
  name: string,
  files: AdapterFileReplaceModel[],
) {
  for (const file of files) {
    yield call(replaceAdapterFile, sysKey, name, file.name, file.file);
  }
}

function* deleteAllFiles(sysKey: string, name: string, files: string[]) {
  for (const file of files) {
    yield call(deleteAdapterFile, sysKey, name, file);
  }
}

// Individual exports for testing
export function* updateAdapterRequest() {
  yield takeEvery(
    ActionConstants.UPDATE_ADAPTER_REQUEST,
    callUpdateAllAdapterInfo,
  );
}

// All sagas to be loaded
export default function*() {
  yield all([updateAdapterRequest()]);
}



