/**
 *
 * NotificationWrapper
 *
 */

import React from 'react';
// import styled from 'styled-components';

interface MyProps {
  valuetowatch: any;
  timeout?: number;
}

interface MyState {
  hasNewValue: boolean;
}

class NotificationWrapper<Fields> extends React.Component<
  Fields & MyProps,
  MyState
> {
  // eslint-disable-line react/prefer-stateless-function

  static defaultProps: Partial<MyProps> = {
    timeout: 1000,
  };

  state: MyState = {
    hasNewValue: false,
  };

  componentWillReceiveProps(nextProps: MyProps) {
    if (nextProps.valuetowatch !== this.props.valuetowatch) {
      this.setState({
        hasNewValue: true,
      });
      setTimeout(() => {
        this.setState({
          hasNewValue: false,
        });
      }, this.props.timeout);
    }
  }
}

export default NotificationWrapper;



