/*
 *
 * AdapterEdgeManager reducer
 *
 */

import assign from 'immutable-assign';
import { ActionTypes } from './actions';
import { ActionConstants } from './actionConstants';
import { EdgeModel } from 'cb-utils/console-entity-models';

export interface State {
  currentTab: string;
  selectedEdges?: string[];
  runningEdges?: EdgeModel[];
  stoppedEdges?: EdgeModel[];
  disconnectedEdges?: EdgeModel[];
  isFetchingAdapters: boolean;
  isCallingCommand: string;
  showLogModal: boolean;
  loggedEdge?: string;
  logs?: string;
}

export const initialState: State = {
  currentTab: 'Running',
  selectedEdges: [],
  runningEdges: [],
  stoppedEdges: [],
  disconnectedEdges: [],
  isFetchingAdapters: false,
  isCallingCommand: '',
  showLogModal: false,
  loggedEdge: null,
  logs: null,
};

function adapterEdgeManagerReducer(
  state: State = initialState,
  action: ActionTypes,
) {
  switch (action.type) {
    case ActionConstants.DEFAULT_ACTION:
      return state;
    case ActionConstants.SELECT_NEW_TAB:
      return assign(state, (m) => {
        m.currentTab = action.payload.status;
        m.selectedEdges = [];
        return m;
      });
    case ActionConstants.TOGGLE_EDGE_ITEM:
      return assign(
        state,
        (m) => m.selectedEdges,
        () => [...action.payload.selectedEdges],
      );
    case ActionConstants.FETCH_EDGES_FOR_ADAPTER_REQUEST:
      return assign(state, (m) => m.isFetchingAdapters, () => true);
    case ActionConstants.FETCH_EDGES_FOR_ADAPTER_SUCCESS:
      return assign(state, (m) => {
        m.runningEdges = [...action.payload.runningEdges];
        m.stoppedEdges = [...action.payload.stoppedEdges];
        m.disconnectedEdges = [...action.payload.disconnectedEdges];
        m.isFetchingAdapters = false;
        m.isCallingCommand = '';
        return m;
      });
    case ActionConstants.CALL_ADAPTER_COMMAND_REQUEST:
      return assign(
        state,
        (m) => m.isCallingCommand,
        () => action.payload.command,
      );
    case ActionConstants.FETCH_LOGS_SUCCESS:
      return assign(state, (m) => {
        m.logs = action.payload.logs;
        m.showLogModal = true;
        m.loggedEdge = action.payload.name;
        return m;
      });
    case ActionConstants.HIDE_LOG_MODAL:
      return assign(state, (m) => {
        m.showLogModal = false;
        m.loggedEdge = null;
        m.logs = null;
        return m;
      });
    default:
      return state;
  }
}

export default adapterEdgeManagerReducer;



