import { createSelector } from 'reselect';
import { STORE_KEY } from './constants';
import { State } from './reducer';
import { AppState } from 'reducers';

/**
 * Direct selector to the adapterDetail state domain
 */
const selectAdapterDetailDomain = () => (state: AppState): State =>
  state[STORE_KEY];

/**
 * Other specific selectors
 */

const selectConfigModalIsOpen = () =>
  createSelector(
    makeSelectAdapterDetail(),
    (substate) => substate.configModalIsOpen,
  );

const selectAdapterFiles = () =>
  createSelector(makeSelectAdapterDetail(), (substate) => substate.files);

const selectIsAdapterUpdating = () =>
  createSelector(
    makeSelectAdapterDetail(),
    (substate) => substate.isUpdatingAdapter,
  );

/**
 * Default selector used by AdapterDetail
 */

const makeSelectAdapterDetail = () =>
  createSelector(selectAdapterDetailDomain(), (substate) => substate);

export default makeSelectAdapterDetail;
export {
  selectAdapterDetailDomain,
  selectConfigModalIsOpen,
  selectAdapterFiles,
  selectIsAdapterUpdating,
};



