/*
*
* AdapterLogModal
*
*/

import React, { SFC } from 'react';
import styled from 'styled-components';
import {
  Button,
  ButtonGroup,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
} from 'reactstrap';
import { FormattedMessage } from 'react-intl';
import messages from './messages';
import { RefreshIcon } from 'components/Icons';
import styleVars from 'cssOverrides/styleVars';

interface AdapterLogModalProps {
  edgeName: string;
  toggle: () => void;
  logs: string;
  refreshLogs: (edgeName: string) => void;
}

const NameWrapper = styled.div`
  display: flex;
  margin: 0 0 0.5rem 0;
  flex-shrink: 0;
`;

const LogWrapper = styled.div`
  display: flex;
  flex-direction: column;
  margin-top: 20px;
`;

const CodeWrapper = styled.code`
  border: 1px solid ${styleVars.gray600};
  border-radius: 5px;
  height: 300px;
  color: black;
  overflow-y: scroll;
`;

const AdapterLogModal: SFC<AdapterLogModalProps> = (props) => {
  return (
    <div>
      <Modal isOpen toggle={props.toggle}>
        <ModalHeader>
          <FormattedMessage {...messages.header} />
        </ModalHeader>
        <ModalBody>
          <NameWrapper>
            <h5>{props.edgeName}</h5>
            <ButtonGroup size="sm" className="ml-auto">
              <Button onClick={() => props.refreshLogs(props.edgeName)}>
                <RefreshIcon />
              </Button>
            </ButtonGroup>
          </NameWrapper>
          <LogWrapper>
            <CodeWrapper>{props.logs}</CodeWrapper>
          </LogWrapper>
        </ModalBody>
        <ModalFooter>
          <Button color="secondary" onClick={props.toggle}>
            Dismiss
          </Button>
        </ModalFooter>
      </Modal>
    </div>
  );
};

export default AdapterLogModal;



