/*
 *
 * AdapterConfigModal
 *
 */

import React from 'react';
import { connect } from 'react-redux';
import { FormattedMessage } from 'react-intl';
import { Button, Label } from 'reactstrap';
import styled from 'styled-components';
import injectSaga from 'utils/injectSaga';
import { compose } from 'redux';
import injectReducer from 'utils/injectReducer';
import {
  selectAdapterReplaceFiles,
  selectAdapterUploadFiles,
  selectModifiableAdapter,
  selectModifiableAdapterFiles,
  selectAdapterIsUpdating,
} from './selectors';
import reducer from './reducer';
import { STORE_KEY } from './constants';
import saga from './sagas';
import messages from './messages';
import InfoModal from 'components/InfoModal';
import AdapterFileManager from 'components/AdapterFileManager';
import {
  AdapterModel,
  AdapterFileModel,
  AdapterFileReplaceModel,
  AdapterFileUploadModel,
  AdapterCommands,
  AdapterConfigMap,
  AdapterFileTypes,
} from 'cb-utils/console-entity-models';
import {
  createAddReplaceFile,
  createAddNewFiles,
  createSetModalAdapterAndFiles,
  createUpdateCommand,
  createDeleteFile,
  createDeleteNewFile,
  createDeleteReplaceFile,
  createUpdateAdapterRequest,
} from './actions';
import { createDismissAdapterConfigModal } from 'containers/AdapterDetail/actions';

import LoadingIndicator from 'components/LoadingIndicator';
import AdapterCommandManager from 'components/AdapterCommandManager';
import { PlusIcon } from 'components/Icons';
import ErrorModal from 'components/ErrorModal';
import { CBDispatch } from 'cb-utils/typeUtils';
import { createStructuredSelector } from 'reselect';

const AdditionalTitleWrap = styled.div`
  display: flex;
`;

const AdditionalAddButton = styled(PlusIcon)`
  margin-left: 10px;
  margin-top: 3px;
  cursor: pointer;
`;

const FileWrapper = styled.input`
  display: none !important;
`;

interface AdapterConfigModalProps {
  adapter: AdapterModel;
  files: AdapterFileModel[];
}

interface AdapterConfigModalState {
  uploadError: boolean;
}

export class AdapterConfigModal extends React.Component<
  AdapterConfigModalProps &
    AdapterConfigModalState &
    DispatchToProps &
    StateToProps
> {
  state = {
    uploadError: false,
  };

  componentWillMount() {
    this.props.init(this.props.adapter, this.props.files);
  }

  render() {
    return (
      <InfoModal
        toggleOpen={this.props.dismissConfigModal}
        isOpen
        title={<FormattedMessage {...messages.modalTitle} />}
        body={this.renderBody()}
        footer={() => this.renderFooter()}
      />
    );
  }

  renderBody() {
    const {
      modifiableFiles,
      uploadFiles,
      replaceFiles,
      adapter,
      modifiableAdapter,
      deleteFile,
      deleteNewFile,
      deleteReplaceFile,
    } = this.props;
    const adapt = modifiableAdapter ? modifiableAdapter : adapter;
    const newFileNames = uploadFiles.map((f) => f.name);
    const replaceFileNames = replaceFiles.map((f) => f.name);
    const oldFileNames = modifiableFiles.map((f) => f.name);
    const fileDict = this.organizeFileDict(
      adapt,
      oldFileNames,
      newFileNames,
      replaceFileNames,
    );
    const commands = Object.keys(fileDict.commands);
    const other = fileDict.other ? Object.keys(fileDict.other) : undefined;
    return (
      <React.Fragment>
        {this.state.uploadError && (
          <ErrorModal
            title="Upload Error"
            onClose={this.dismissErrorModal}
            body="Replacement files must have the same name as the file they are replacing"
          />
        )}
        {commands.length > 0
          ? commands.map((command: AdapterCommands) => {
              return (
                <AdapterCommandManager
                  key={`commandkey${command}`}
                  commandName={command}
                  command={adapt[command]}
                  fileName={fileDict.commands[command as AdapterCommands].file}
                  fileType={fileDict.commands[command as AdapterCommands].type}
                  onCommandChange={this.callOnCommandChange}
                  uploadFiles={this.uploadFiles}
                  deleteFile={
                    fileDict.commands[command as AdapterCommands].type ===
                    AdapterFileTypes.new
                      ? deleteNewFile
                      : fileDict.commands[command as AdapterCommands].type ===
                        AdapterFileTypes.replace
                        ? deleteReplaceFile
                        : deleteFile
                  }
                />
              );
            })
          : null}
        <AdditionalTitleWrap>
          <h5>
            <FormattedMessage {...messages.additionalFiles} />
          </h5>
          <Label htmlFor={`uploadedFile`}>
            <AdditionalAddButton />
          </Label>
          <FileWrapper
            type="file"
            id={`uploadedFile`}
            accept="*"
            onChange={(e) => this.uploadFiles(e, '', AdapterFileTypes.new)}
          />
        </AdditionalTitleWrap>
        {other
          ? other.map((file: string) => {
              return (
                <AdapterFileManager
                  key={`filekey${file}`}
                  commandName=""
                  fileName={file}
                  fileType={fileDict.other[file]}
                  uploadFiles={(e) =>
                    this.uploadFiles(e, file, fileDict.other[file])
                  }
                  deleteFile={
                    fileDict.other[file] === AdapterFileTypes.new
                      ? deleteNewFile
                      : fileDict.other[file] === AdapterFileTypes.replace
                        ? deleteReplaceFile
                        : deleteFile
                  }
                />
              );
            })
          : null}
      </React.Fragment>
    );
  }

  renderFooter() {
    const { updateAdapterRequest, isUpdating, adapter } = this.props;
    const url = window.location.href.split('/');
    const sysKey = url[5];
    const name = adapter.name;
    return (
      <Button
        type="submit"
        disabled={name === ''}
        color="primary"
        onClick={() => updateAdapterRequest(sysKey, name)}
        data-cy="adapter-config-submit"
      >
        {isUpdating ? (
          <LoadingIndicator />
        ) : (
          <FormattedMessage {...messages.updateAdapter} />
        )}
      </Button>
    );
  }

  uploadFiles = (
    e: any,
    fileName: string,
    fileType: string,
    command?: string,
  ) => {
    const files = e.target.files;
    if (files.length > 0) {
      if (
        fileType === AdapterFileTypes.old ||
        fileType === AdapterFileTypes.replace
      ) {
        const file = files[0];
        if (file.name === fileName) {
          this.readFile(file, false).then((res) => {
            this.props.addReplaceFile(res, fileName);
          });
        } else {
          this.setState({
            uploadError: true,
          });
        }
      } else {
        if (command && this.props.modifiableAdapter[command].length === 0) {
          this.callOnCommandChange(command, `./${files[0].name}`);
        }
        Promise.all(
          Array.prototype.map.call(files, (file: File) =>
            this.readFile(file, true),
          ),
        ).then((res) => {
          this.props.addNewFiles(res);
        });
      }
    }
  };

  readFile = (file: File, isNew: boolean) => {
    const reader = new FileReader();
    return new Promise((resolve, reject) => {
      reader.readAsDataURL(file);
      reader.onloadend = () => {
        const encodedContent = reader.result.split(',')[1];
        let item: AdapterFileReplaceModel | AdapterFileUploadModel;
        if (isNew) {
          item = {
            file: encodedContent,
            name: file.name,
            adaptor_name: this.props.adapter.name,
            path_name: file.name,
          };
        } else {
          item = {
            file: encodedContent,
            name: file.name,
          };
        }
        resolve(item);
      };
    });
  };

  organizeFileDict = (
    adapt: AdapterModel,
    old: string[],
    newF: string[],
    replace: string[],
  ) => {
    const fileDict: AdapterConfigMap = {
      commands: {
        [AdapterCommands.deploy]: {
          file: '',
        },
        [AdapterCommands.start]: {
          file: '',
        },
        [AdapterCommands.stop]: {
          file: '',
        },
        [AdapterCommands.status]: {
          file: '',
        },
        [AdapterCommands.logs]: {
          file: '',
        },
        [AdapterCommands.undeploy]: {
          file: '',
        },
      },
    };
    const dictAfterOld = this.sortThroughFileArray(
      adapt,
      old,
      AdapterFileTypes.old,
      fileDict,
    );
    const dictAfterNew = this.sortThroughFileArray(
      adapt,
      newF,
      AdapterFileTypes.new,
      dictAfterOld,
    );
    const finalDict = this.sortThroughFileArray(
      adapt,
      replace,
      AdapterFileTypes.replace,
      dictAfterNew,
    );

    return finalDict;
  };

  sortThroughFileArray(
    adapt: AdapterModel,
    array: string[],
    type: AdapterFileTypes,
    dictionary: AdapterConfigMap,
  ) {
    let newDictionary = { ...dictionary };
    for (const file of array) {
      let isCommandFile;
      const commands = newDictionary.commands;
      const keys = Object.keys(commands);
      for (const command of keys) {
        if (commands[command as AdapterCommands].file.length === 0) {
          if (
            adapt[command].split('/')[1] &&
            adapt[command].split('/')[1] === file
          ) {
            commands[command as AdapterCommands].file = file;
            commands[command as AdapterCommands].type = type;
            isCommandFile = true;
            continue;
          } else if (
            adapt[command].split('/')[2] &&
            adapt[command].split('/')[2] === file
          ) {
            commands[command as AdapterCommands].file = file;
            commands[command as AdapterCommands].type = type;
            isCommandFile = true;
            continue;
          }
        }
      }
      if (!isCommandFile) {
        if (newDictionary.other) {
          newDictionary.other[file] = type;
        } else {
          newDictionary = {
            ...newDictionary,
            other: {
              [file]: type,
            },
          };
        }
      }
    }
    return newDictionary;
  }

  callOnCommandChange = (name: string, command: string) => {
    this.props.onCommandChange({
      ...this.props.modifiableAdapter,
      [name]: command,
    });
  };

  findCommandFile(command: string, files: string[]): string {
    for (const file of files) {
      if (command.split('/')[1] && command.split('/')[1] === file) {
        return file;
      } else if (command.split('/')[2] && command.split('/')[2] === file) {
        return file;
      }
    }
    return '';
  }

  dismissErrorModal = () => {
    this.setState({
      uploadError: false,
    });
  };
}

type StateToProps = ReturnType<typeof mapStateToProps>;

const mapStateToProps = createStructuredSelector({
  modifiableAdapter: selectModifiableAdapter(),
  modifiableFiles: selectModifiableAdapterFiles(),
  uploadFiles: selectAdapterUploadFiles(),
  replaceFiles: selectAdapterReplaceFiles(),
  isUpdating: selectAdapterIsUpdating(),
});

type DispatchToProps = ReturnType<typeof mapDispatchToProps>;
function mapDispatchToProps(dispatch: CBDispatch) {
  return {
    init: (adapter: AdapterModel, files: AdapterFileModel[]) =>
      dispatch(createSetModalAdapterAndFiles(adapter, files)),
    dismissConfigModal: () => dispatch(createDismissAdapterConfigModal()),
    addReplaceFile: (file: AdapterFileReplaceModel | {}, name: string) =>
      dispatch(createAddReplaceFile(file, name)),
    addNewFiles: (files: AdapterFileUploadModel[] | any[]) =>
      dispatch(createAddNewFiles(files)),
    deleteFile: (name: string) => dispatch(createDeleteFile(name)),
    deleteNewFile: (name: string) => dispatch(createDeleteNewFile(name)),
    deleteReplaceFile: (name: string) =>
      dispatch(createDeleteReplaceFile(name)),
    onCommandChange: (adapter: AdapterModel) =>
      dispatch(createUpdateCommand(adapter)),
    updateAdapterRequest: (sysKey: string, name: string) =>
      dispatch(createUpdateAdapterRequest(sysKey, name)),
  };
}

const withConnect = connect(mapStateToProps, mapDispatchToProps);

const withReducer = injectReducer({ key: STORE_KEY, reducer });
const withSaga = injectSaga({ key: STORE_KEY, saga });
export default compose(withReducer, withSaga, withConnect)(
  AdapterConfigModal,
) as React.ComponentType<AdapterConfigModalProps>;



