/*
 * AdapterEdgeManager Messages
 *
 * This contains all the text for the AdapterEdgeManager component.
 */
import { defineMessages } from 'react-intl';

const mess = defineMessages({
  header: {
    id: 'app.containers.AdapterEdgeManager.header',
    defaultMessage: 'Edge Management',
  },
  edgesSelected: {
    id: 'app.containers.AdapterEdgeManager.edgesSelected',
    defaultMessage: `{number} edge(s) selected`,
  },
  callingCommand: {
    id: 'app.containers.AdapterEdgeManager.callingCommand',
    defaultMessage: `Calling {command} command on {numEdges} edge(s)`,
  },
  fetchingAdapters: {
    id: 'app.containers.AdapterEdgeManager.fetchingAdapters',
    defaultMessage: `Fetching adapter status on edges`,
  },
});

export default mess;



