/**
 *
 * FilterModal
 *
 */

import React from 'react';
// import styled from 'styled-components';

import { Modal, ModalHeader, ModalBody, ModalFooter, Button } from 'reactstrap';

import { cb } from 'cb-utils/cbLib';

import { FormattedMessage } from 'react-intl';
import messages from './messages';

import ConditionBuilder, {
  Field,
  operatorToMethodName,
} from 'components/ConditionBuilder';
import { TableColumn } from 'containers/Console/state';
import {
  Condition,
  Rule,
  isCondition,
  createBlankCondition,
  OR_OPERATOR,
} from 'cb-utils/console-entity-models/FilterCondition';

interface FilterModalProps {
  condition: Condition;
  onDismiss: () => void;
  onApply: (q: QueryObj, cond: Condition) => void;
  columns: TableColumn[];
}

interface FilterModalState {
  condition: Condition;
}

const formatColumns = (cols: TableColumn[]): Field[] => {
  return cols.map((c): Field => ({
    name: c.ColumnName,
    label: c.ColumnName,
  }));
};

const callQueryMethod = (query: QueryObj, r: Rule) => {
  query[operatorToMethodName[r.operator].method](r.field, r.value);
};

const createQueryFromRules = (rules: Rule[]) => {
  const q = cb.Query();
  rules.forEach((r) => {
    callQueryMethod(q, r);
  });
  return q;
};

// WARNING: this code sucks. you have been warned
const createQueryFromCondition = (condition: Condition): QueryObj => {
  if (condition.combinator === OR_OPERATOR) {
    const q = cb.Query();
    condition.rules.forEach((r) => {
      let orQuery;
      if (isCondition(r)) {
        orQuery = createQueryFromRules(r.rules);
      } else {
        orQuery = cb.Query();
        callQueryMethod(orQuery, r);
      }
      q.or(orQuery);
    });
    return q;
  } else {
    return createQueryFromRules(condition.rules);
  }
};

class FilterModal extends React.Component<FilterModalProps, FilterModalState> {
  // eslint-disable-line react/prefer-stateless-function

  state = {
    condition: this.props.condition,
  };

  render() {
    const { onDismiss, columns } = this.props;
    const { condition } = this.state;
    return (
      // add the noDrag className so that we can type in this modal while using the CRUD grid in a portal that is in edit mode
      <Modal isOpen toggle={onDismiss} className="noDrag">
        <ModalHeader toggle={onDismiss}>
          <FormattedMessage {...messages.header} />
        </ModalHeader>
        <ModalBody>
          <ConditionBuilder
            onChange={this._onConditionChange}
            condition={condition}
            useCustomFields
            maxGroupLevel={1}
            fields={formatColumns(columns)}
          />
        </ModalBody>
        <ModalFooter>
          <Button color="default" onClick={onDismiss}>
            Cancel
          </Button>
          <Button color="danger" onClick={this._clearConditions}>
            Clear All
          </Button>
          <Button color="primary" onClick={this._onFilter}>
            Filter
          </Button>
        </ModalFooter>
      </Modal>
    );
  }

  _clearConditions = () => {
    this.setState({
      ...this.state,
      condition: createBlankCondition(),
    });
  };

  _onConditionChange = (condition: Condition) => {
    this.setState({
      ...this.state,
      condition,
    });
  };

  _onFilter = () => {
    const q = createQueryFromCondition(this.state.condition);
    this.props.onApply(q, this.state.condition);
  };
}

export default FilterModal;



