/*
 *
 * AdapterDetail reducer
 *
 */

import assign from 'immutable-assign';
import { ActionTypes } from './actions';
import { ActionConstants } from './actionConstants';
import { AdapterFileModel } from 'cb-utils/console-entity-models';

export interface State {
  configModalIsOpen: boolean;
  files: AdapterFileModel[];
  isUpdatingAdapter: boolean;
}

export const initialState: State = {
  configModalIsOpen: false,
  files: [],
  isUpdatingAdapter: false,
};

function adapterDetailReducer(
  state: State = initialState,
  action: ActionTypes,
) {
  switch (action.type) {
    case ActionConstants.SET_ADAPTER_FILES:
      return assign(state, (m) => m.files, () => action.payload.files);
    case ActionConstants.SHOW_CONFIG_MODAL:
      return assign(state, (m) => m.configModalIsOpen, () => true);
    case ActionConstants.HIDE_CONFIG_MODAL:
      return assign(state, (m) => m.configModalIsOpen, () => false);
    case ActionConstants.UPDATE_ADAPTER_INFO:
      return assign(state, (m) => m.isUpdatingAdapter, () => true);
    case ActionConstants.UPDATE_ADAPTER_INFO_SUCCESS:
      return assign(state, (m) => m.isUpdatingAdapter, () => false);
    case ActionConstants.UPDATE_ADAPTER_INFO_ERROR:
      return assign(state, (m) => m.isUpdatingAdapter, () => false);
    default:
      return state;
  }
}

export default adapterDetailReducer;



