/**
 *
 * AdapterFileManager
 *
 */

import React from 'react';
import styled from 'styled-components';
import { Row, Col } from 'reactstrap';
import { FormattedMessage } from 'react-intl';
import { DownloadIcon, UploadIcon, TrashIcon } from 'components/Icons';
import { fetchAdapterFile } from 'cb-utils/services/adapters';
import withStyledProps from 'cb-utils/withStyledProps';
import styleVars from 'cssOverrides/styleVars';
import messages from './messages';
import { AdapterFileTypes } from 'cb-utils/console-entity-models';

const NameWrapper = styled.div`
  color: ${styleVars.gray600};
`;

const ColWrapper = styled(Col)`
  text-align: center;
`;

const ButtonWrapper = styled.div`
  color: ${styleVars.primary};
`;

const DownloadButton = withStyledProps<Partial<AdapterFileManagerProps>>()(
  styled.div,
)`
  color: ${(props) =>
    props.fileType === AdapterFileTypes.old
      ? styleVars.primary
      : styleVars.gray600};
  cursor: ${(props) =>
    props.fileType === AdapterFileTypes.old ? 'pointer' : 'auto'};
  pointer-events: ${(props) =>
    props.fileType === AdapterFileTypes.old ? 'auto' : 'none'};
`;

const TrashButton = withStyledProps<Partial<AdapterFileManagerProps>>()(
  styled(TrashIcon),
)`
  color: ${(props) =>
    props.fileName.length > 0 ? styleVars.primary : styleVars.gray600};
  cursor: ${(props) => (props.fileName.length > 0 ? 'pointer' : 'auto')};
  pointer-events: ${(props) => (props.fileName.length > 0 ? 'auto' : 'none')};
`;

const LabelWrapper = styled.label`
  cursor: pointer;
`;

const FileWrapper = styled.input`
  display: none !important;
`;

interface AdapterFileManagerProps {
  fileName: string;
  fileType: string;
  uploadFiles: (e: any, name: string, type: string, command: string) => void;
  deleteFile: (name: string) => void;
  commandName: string;
}

class AdapterFileManager extends React.PureComponent<
  AdapterFileManagerProps,
  {}
> {
  static defaultProps: Partial<AdapterFileManagerProps> = {
    fileName: '',
    fileType: AdapterFileTypes.new,
    commandName: '',
  };

  url = window.location.href.split('/');
  sysKey = this.url[5];
  adapterName = this.url[7];

  downloadFile = () => {
    fetchAdapterFile(this.sysKey, this.adapterName, this.props.fileName).then(
      (file) => {
        const text = atob(file.file);
        const element = document.createElement('a');
        element.setAttribute(
          'href',
          'data:text/plain;charset=utf-8,' + encodeURIComponent(text),
        );
        element.setAttribute('download', this.props.fileName);
        element.style.display = 'none';
        document.body.appendChild(element);
        element.click();
        document.body.removeChild(element);
      },
    );
  };

  deleteFile = () => {
    if (this.props.fileType === AdapterFileTypes.new) {
      this.props.deleteFile(this.props.fileName);
    } else if (this.props.fileType === AdapterFileTypes.replace) {
      this.props.deleteFile(this.props.fileName);
    } else {
      this.props.deleteFile(this.props.fileName);
    }
  };

  render() {
    const { fileName, commandName, fileType } = this.props;
    const cName = commandName.length > 0 ? commandName : fileName;
    return (
      <Row>
        <ColWrapper>
          <NameWrapper>{fileName}</NameWrapper>
        </ColWrapper>
        <ColWrapper>
          <ButtonWrapper>
            <LabelWrapper htmlFor={`uploaded${cName}File`}>
              <UploadIcon />
              {fileName.length > 0 ? (
                <FormattedMessage {...messages.replace} />
              ) : (
                <FormattedMessage {...messages.upload} />
              )}
            </LabelWrapper>
            <FileWrapper
              type="file"
              id={`uploaded${cName}File`}
              accept="*"
              onChange={(e) =>
                this.props.uploadFiles(e, fileName, fileType, commandName)
              }
            />
          </ButtonWrapper>
        </ColWrapper>
        <ColWrapper>
          <DownloadButton
            onClick={this.downloadFile}
            fileName={fileName}
            fileType={fileType}
          >
            <DownloadIcon />
            <FormattedMessage {...messages.download} />
          </DownloadButton>
        </ColWrapper>
        <ColWrapper>
          <ButtonWrapper>
            <TrashButton onClick={this.deleteFile} fileName={fileName} />
          </ButtonWrapper>
        </ColWrapper>
      </Row>
    );
  }
}

export default AdapterFileManager;



