import { all, takeEvery, put, call } from 'redux-saga/effects';
import { ActionConstants } from './actionConstants';
import {
  createSetAdapterFiles,
  FetchAdapterFiles,
  createHideAdapterConfigModal,
  UpdateAdapterInfo,
  createUpdateAdapterInfoSuccess,
  createUpdateAdapterInfoError,
} from './actions';
import { adapterActions } from '../Console/ducks';
import {
  createNavToOverview,
  createNewNotification,
  createDismissAdapterInfoModal,
} from 'containers/Console/actions/ui';
import { adaptersRoot } from 'containers/Console/routes';
import { AdapterFileModel } from 'cb-utils/console-entity-models';
import { fetchAdapterFiles, updateAdapter } from 'cb-utils/services/adapters';
import { createClearConfigModalInfo } from 'containers/AdapterConfigModal/actions';

function* goToAdaptersOverview() {
  yield put(createNavToOverview(adaptersRoot));
}

// Individual exports for testing

function* callFetchAdapterFiles(action: FetchAdapterFiles) {
  yield put(adapterActions.fetchAll.request(action.payload.sysKey));
  const files: AdapterFileModel[] = yield call(
    fetchAdapterFiles,
    action.payload.sysKey,
    action.payload.name,
  );
  yield put(createSetAdapterFiles(files));
}

function* callClearAndDismissModal() {
  yield put(createClearConfigModalInfo());
  yield put(createHideAdapterConfigModal());
}

function* callUpdateAdapterInfo(action: UpdateAdapterInfo) {
  delete action.payload.adapter.name;
  delete action.payload.adapter.version;
  delete action.payload.adapter.protocol;
  try {
    yield call(
      updateAdapter,
      action.payload.sysKey,
      action.payload.name,
      action.payload.adapter,
    );
    yield put(createUpdateAdapterInfoSuccess());
    yield put(adapterActions.fetchAll.request(action.payload.sysKey));
    yield put(createDismissAdapterInfoModal());
    yield put(
      createNewNotification({
        title: 'Adapter successfully updated',
      }),
    );
  } catch (e) {
    yield put(createUpdateAdapterInfoError());
  }
}

export function* setAdapterForDetail() {
  yield takeEvery(ActionConstants.FETCH_ADAPTER_FILES, callFetchAdapterFiles);
}

export function* adapterDeleteSuccessful() {
  yield takeEvery(adapterActions.delete.successType, function*() {
    yield goToAdaptersOverview();
    yield put(
      createNewNotification({
        title: 'Adapter successfully deleted',
      }),
    );
  });
}

export function* dismissConfigModal() {
  yield takeEvery(
    ActionConstants.DISMISS_CONFIG_MODAL,
    callClearAndDismissModal,
  );
}

export function* updateAdapterInfoRequest() {
  yield takeEvery(ActionConstants.UPDATE_ADAPTER_INFO, callUpdateAdapterInfo);
}

// All sagas to be loaded
export default function*() {
  yield all([
    adapterDeleteSuccessful(),
    setAdapterForDetail(),
    dismissConfigModal(),
    updateAdapterInfoRequest(),
  ]);
}



