/*
 *
 * AdapterDetail
 *
 */

import React from 'react';
import { connect } from 'react-redux';
import styled from 'styled-components';
import Helmet from 'react-helmet';
import { FormattedMessage } from 'react-intl';
import injectSaga from 'utils/injectSaga';
import { compose } from 'redux';
import injectReducer from 'utils/injectReducer';
import {
  selectConfigModalIsOpen,
  selectAdapterFiles,
  selectIsAdapterUpdating,
} from './selectors';
import reducer from './reducer';
import { STORE_KEY } from './constants';
import saga from './sagas';
import messages from './messages';
import * as ducks from 'containers/Console/ducks';
import {
  createShowConfirmationModal,
  createOnAddNewAdapter,
  createDismissAdapterInfoModal,
} from 'containers/Console/actions/ui';
import ConsolePage from 'components/ConsolePage';
import PageHeader from 'components/ConsolePageHeader';
import PageFooter from 'components/ConsolePageFooter';
import { Button } from 'reactstrap';
import { TrashIcon, PencilIcon } from 'components/EntityIcons';
import { SystemRouteProps } from 'cb-utils/systemRouteProps';
import {
  AdapterModel,
  AdapterCommands,
  AdapterFileMap,
} from 'cb-utils/console-entity-models';
import {
  createFetchAdapterFiles,
  createShowAdapterConfigModal,
  createUpdateAdapterInfo,
} from './actions';
import LoadingIndicator from 'components/WithLoadable/LoadingIndicator';
import AdapterConfig from 'components/AdapterConfig';
import AdapterConfigModal from 'containers/AdapterConfigModal';
import { selectAdapterByName } from 'containers/Console/selectors/adapters';
import { selectShowAdapterInfoModal } from 'containers/Console/selectors/ui';
import AdapterInfoModal from 'components/AdapterInfoModal';
import { CBDispatch } from 'cb-utils/typeUtils';
import AdapterEdgeManager from 'containers/AdapterEdgeManager';
import { overallUrls } from 'cb-utils/getUrls';
import { AppState } from 'reducers';
import { createStructuredSelector } from 'reselect';

const ArchWrapper = styled.div`
  color: grey;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
`;

const IconWrapper = styled.div`
  display: inline-block;
  color: black;
  margin-left: 1rem;
  cursor: pointer;
`;

const DetailWrapper = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
`;

type AdapterDetailProps = SystemRouteProps<{
  adapterName: string;
}>;

const getAdapterName = (props: AdapterDetailProps): string =>
  props.match.params.adapterName;

export class AdapterDetail extends React.Component<
  AdapterDetailProps & DispatchToProps & StateToProps,
  {}
> {
  componentWillMount() {
    this.props.init();
  }

  render() {
    const {
      adapter,
      showConfigModal,
      configModalIsOpen,
      files,
      hasFetchedAll,
    } = this.props;
    if (hasFetchedAll && !adapter) {
      this.props.history.push(overallUrls.adapters());
    }
    const fileNames = files.map((file) => file.name);
    const fileDict = this.organizeCommandFiles(fileNames, adapter);
    const sysKey = this.props.match.params.systemKey;
    const adapterName = getAdapterName(this.props);

    return (
      <React.Fragment>
        <Helmet
          title="ClearBlade - Adapters"
          meta={[
            { name: 'description', content: 'Description of AdapterDetail' },
          ]}
        />
        <ConsolePage subheader={this._renderSubHeader()}>
          {adapter ? (
            <DetailWrapper>
              <div>
                <AdapterConfig
                  adapter={adapter}
                  showConfigModal={showConfigModal}
                  fileDict={fileDict}
                />
                {configModalIsOpen && (
                  <AdapterConfigModal files={files} adapter={adapter} />
                )}
              </div>
              <div>
                <AdapterEdgeManager
                  systemKey={sysKey}
                  adapterName={adapterName}
                />
              </div>
            </DetailWrapper>
          ) : (
            <LoadingIndicator forPage />
          )}
        </ConsolePage>
        <PageFooter leftButtons={this.renderDeleteButton} />
      </React.Fragment>
    );
  }

  _renderSubHeader = () => {
    const name = this.props.adapter && this.props.adapter.name;
    if (name) {
      return (
        <React.Fragment>
          <PageHeader
            header={<div>{name}</div>}
            subHeader={
              <div>
                {this.props.showEditAdapterInfoModal && (
                  <AdapterInfoModal
                    toggle={this.props.dismissAdapterInfoModal}
                    submit={(adapter: AdapterModel) =>
                      this.props.editAdapterInfo(adapter)
                    }
                    name={name}
                    description={this.props.adapter.description}
                    architecture={this.props.adapter.architecture}
                    isSubmitting={this.props.isEditingAdapter}
                    error={''}
                  />
                )}
                <ArchWrapper>
                  <div>
                    <FormattedMessage {...messages.architecture} />
                    {`: ${this.formatArchitecture(
                      this.props.adapter.architecture,
                    )}`}
                    <IconWrapper id="edit-adapter-icon">
                      <PencilIcon onClick={this.props.onEditAdapter} />
                    </IconWrapper>
                  </div>
                  <div>{this.props.adapter.description}</div>
                </ArchWrapper>
              </div>
            }
          />
        </React.Fragment>
      );
    }
    return null;
  };

  renderDeleteButton = () => {
    const { deleteAdapterRequest } = this.props;
    const name = getAdapterName(this.props);

    if (name) {
      return (
        <Button
          color="link"
          onClick={() => deleteAdapterRequest()}
          data-cy="delete-adapter"
        >
          <TrashIcon />
          &nbsp;
          <FormattedMessage {...messages.deleteAdapter} />
        </Button>
      );
    }
    return null;
  };

  handleDescriptionUpdate = (val: string) => {
    this.setState({
      description: val,
    });
  };

  organizeCommandFiles = (files: string[], adapter: AdapterModel) => {
    if (adapter) {
      const commandFiles: AdapterFileMap = {
        [AdapterCommands.deploy]: '',
        [AdapterCommands.start]: '',
        [AdapterCommands.stop]: '',
        [AdapterCommands.status]: '',
        [AdapterCommands.logs]: '',
        [AdapterCommands.undeploy]: '',
      };

      for (const file of files) {
        const keys = Object.keys(commandFiles);
        for (const command of keys) {
          if (
            adapter[command].split('/')[1] &&
            adapter[command].split('/')[1] === file
          ) {
            commandFiles[command as AdapterCommands] = file;
            continue;
          } else if (
            adapter[command].split('/')[2] &&
            adapter[command].split('/')[2] === file
          ) {
            commandFiles[command as AdapterCommands] = file;
            continue;
          }
        }
      }
      return commandFiles;
    }
  };

  formatArchitecture(arch: string) {
    switch (arch) {
      case 'linux386':
        return 'Linux 32bit - i386';
      case 'linux32arm':
        return 'Linux 32bit - ARM';
      case 'linux64amd':
        return 'Linux 64bit - AMD';
      case 'linux64arm':
        return 'Linux 64bit - ARM';
      case 'MacOS64':
        return 'MacOS 64bit';
    }
  }
}

const mapStateToProps = (state: AppState, ownProps: AdapterDetailProps) => {
  const name = getAdapterName(ownProps);
  return createStructuredSelector({
    adapter: selectAdapterByName(name),
    configModalIsOpen: selectConfigModalIsOpen(),
    files: selectAdapterFiles(),
    showEditAdapterInfoModal: selectShowAdapterInfoModal(),
    isEditingAdapter: selectIsAdapterUpdating(),
    hasFetchedAll: ducks.adapters.selectors.fetchAll.hasFetched,
  });
};

type StateToProps = ReturnType<ReturnType<typeof mapStateToProps>>;

function mapDispatchToProps(
  dispatch: CBDispatch,
  ownProps: AdapterDetailProps,
) {
  const name = getAdapterName(ownProps);
  const sysKey = ownProps.match.params.systemKey;
  return {
    init: () => dispatch(createFetchAdapterFiles(sysKey, name)),
    deleteAdapterRequest: () =>
      dispatch(
        createShowConfirmationModal(
          `Are you sure you want to delete '${name}'?`,
          ducks.adapters.actions.delete.request(name),
        ),
      ),
    showConfigModal: () => dispatch(createShowAdapterConfigModal()),
    onEditAdapter: () => dispatch(createOnAddNewAdapter()),
    editAdapterInfo: (adapter: AdapterModel) =>
      dispatch(createUpdateAdapterInfo(sysKey, name, adapter)),
    dismissAdapterInfoModal: () => dispatch(createDismissAdapterInfoModal()),
  };
}

type DispatchToProps = ReturnType<typeof mapDispatchToProps>;

const withConnect = connect(mapStateToProps, mapDispatchToProps);

const withReducer = injectReducer({ key: STORE_KEY, reducer });
const withSaga = injectSaga({ key: STORE_KEY, saga });
export default compose(withReducer, withSaga, withConnect)(AdapterDetail);



