const convertArrayOfObjectsToCSV = (incomingData: any[] = []) => {
  if (incomingData.length === 0) {
    return null;
  }

  const columnDelimiter = ',';
  const lineDelimiter = '\n';

  const keys = Object.keys(incomingData[0]);

  let result = '';
  result += keys.join(columnDelimiter);
  result += lineDelimiter;

  incomingData.forEach((item) => {
    let ctr = 0;
    keys.forEach((key) => {
      if (ctr > 0) result += columnDelimiter;

      result += item[key];
      ctr += 1;
    });
    result += lineDelimiter;
  });

  return result;
};

export const downloadCSV = (incomingData: any[]) => {
  let csv = convertArrayOfObjectsToCSV(incomingData);
  if (csv == null) return;

  const filename = 'clearblade_data_table.csv';

  if (!csv.match(/^data:text\/csv/i)) {
    csv = `data:text/csv;charset=utf-8,${csv}`;
  }
  const data = encodeURI(csv);
  const link = document.createElement('a');
  link.setAttribute('href', data);
  link.setAttribute('download', filename);
  link.click();
};



