import React from 'react';
import { FormattedMessage } from 'react-intl';
import messages from './messages';
import { IAdapterCommands } from 'cb-utils/console-entity-models';

const formatAdapterCommandTitle = (commandName: keyof IAdapterCommands) => {
  switch (commandName) {
    case 'deploy_command':
      return <FormattedMessage {...messages.deployCommand} />;
    case 'start_command':
      return <FormattedMessage {...messages.startCommand} />;
    case 'stop_command':
      return <FormattedMessage {...messages.stopCommand} />;
    case 'status_command':
      return <FormattedMessage {...messages.statusCommand} />;
    case 'logs_command':
      return <FormattedMessage {...messages.logsCommand} />;
    case 'undeploy_command':
      return <FormattedMessage {...messages.undeployCommand} />;
  }
};

export default formatAdapterCommandTitle;



