import { createSelector } from 'reselect';
import { STORE_KEY } from './constants';
import { State } from './reducer';
import { AppState } from 'reducers';

/**
 * Direct selector to the adapterEdgeManager state domain
 */
const selectAdapterEdgeManagerDomain = () => (state: AppState): State =>
  state[STORE_KEY];

/**
 * Other specific selectors
 */

const makeSelectCurrentTab = () =>
  createSelector(
    selectAdapterEdgeManagerDomain(),
    (substate) => substate.currentTab,
  );

const makeSelectSelectedEdges = () =>
  createSelector(
    selectAdapterEdgeManagerDomain(),
    (substate) => substate.selectedEdges,
  );

const makeSelectIsFetchingAdapters = () =>
  createSelector(
    selectAdapterEdgeManagerDomain(),
    (substate) => substate.isFetchingAdapters,
  );

const makeSelectIsCallingCommand = () =>
  createSelector(
    selectAdapterEdgeManagerDomain(),
    (substate) => substate.isCallingCommand,
  );

const makeSelectRunningEdges = () =>
  createSelector(
    selectAdapterEdgeManagerDomain(),
    (substate) => substate.runningEdges,
  );

const makeSelectStoppedEdges = () =>
  createSelector(
    selectAdapterEdgeManagerDomain(),
    (substate) => substate.stoppedEdges,
  );

const makeSelectDisconnectedEdges = () =>
  createSelector(
    selectAdapterEdgeManagerDomain(),
    (substate) => substate.disconnectedEdges,
  );

const makeSelectShowLogModal = () =>
  createSelector(
    selectAdapterEdgeManagerDomain(),
    (substate) => substate.showLogModal,
  );

const makeSelectLogs = () =>
  createSelector(selectAdapterEdgeManagerDomain(), (substate) => substate.logs);

const makeSelectLoggedEdge = () =>
  createSelector(
    selectAdapterEdgeManagerDomain(),
    (substate) => substate.loggedEdge,
  );

/**
 * Default selector used by AdapterEdgeManager
 */

const makeSelectAdapterEdgeManager = () =>
  createSelector(selectAdapterEdgeManagerDomain(), (substate) => substate);

export default makeSelectAdapterEdgeManager;
export {
  selectAdapterEdgeManagerDomain,
  makeSelectCurrentTab,
  makeSelectSelectedEdges,
  makeSelectIsFetchingAdapters,
  makeSelectIsCallingCommand,
  makeSelectRunningEdges,
  makeSelectStoppedEdges,
  makeSelectDisconnectedEdges,
  makeSelectShowLogModal,
  makeSelectLoggedEdge,
  makeSelectLogs,
};



