/**
 *
 * AdapterConfig
 *
 */

import React from 'react';
import styled from 'styled-components';

import { Input } from 'reactstrap';
import Card, { CardContent } from 'material-ui-next/Card';

import { FormattedMessage } from 'react-intl';
import messages from './messages';
import styleVars from 'cssOverrides/styleVars';

import {
  AdapterModel,
  AdapterFileMap,
  AdapterCommands,
} from 'cb-utils/console-entity-models';
import { PencilIcon } from 'components/EntityIcons';
import formatAdapterCommandTitle from './utils';
import { CodeFileIcon } from 'components/Icons';

const Heading = styled.div`
  display: flex;
  flex-wrap: wrap;
`;

const IconWrapper = styled.div`
  margin-left: 1rem;
  display: inline-block;
  cursor: pointer;
`;

const AllCommandWrap = styled.div`
  display: flex;
  flex-wrap: wrap;
`;

const CommandFileWrapper = styled.div`
  color: ${styleVars.gray600};
`;

const CommandInputWrapper = styled(Input)`
  color: ${styleVars.gray600};
  background-color: white !important;
`;

const CommandWrapper = styled.div`
  width: 30%;
  margin-bottom: 0.8rem;
  margin-right: 1rem;
`;

const ConfigWrap = styled.div`
  margin-bottom: 20px;
`;

const SetupContent = styled.div`
  color: ${styleVars.primary};
`;

interface AdapterConfigProps {
  adapter: AdapterModel;
  showConfigModal: () => void;
  fileDict: AdapterFileMap;
}

class AdapterConfig extends React.PureComponent<AdapterConfigProps, {}> {
  // eslint-disable-line react/prefer-stateless-function

  render() {
    const needsSetup = this.needsSetup();
    return (
      <ConfigWrap>
        <Heading>
          <h4>
            <FormattedMessage {...messages.configuration} />
          </h4>
          {!needsSetup ? (
            <IconWrapper>
              <PencilIcon onClick={this.props.showConfigModal} />
            </IconWrapper>
          ) : null}
        </Heading>
        <Card>
          <div data-cy="adapter-config-card">
            {needsSetup ? (
              <SetupContent>
                <CardContent onClick={this.props.showConfigModal}>
                  Setup adapter configuration
                </CardContent>
              </SetupContent>
            ) : (
              <CardContent>{this.renderAdapterStats()}</CardContent>
            )}
          </div>
        </Card>
      </ConfigWrap>
    );
  }

  needsSetup = (): boolean => {
    const { adapter } = this.props;
    if (
      adapter.deploy_command.length === 0 &&
      adapter.logs_command.length === 0 &&
      adapter.start_command.length === 0 &&
      adapter.status_command.length === 0 &&
      adapter.stop_command.length === 0 &&
      adapter.undeploy_command.length === 0
    ) {
      return true;
    }
    return false;
  };

  renderAdapterStats = () => {
    const { fileDict, adapter } = this.props;
    const commandKeys = Object.keys(fileDict);
    return (
      <AllCommandWrap data-cy="adapter-stats">
        {commandKeys.map((command) => {
          return (
            <CommandWrapper key={`keyfor${command}`}>
              <h5>{formatAdapterCommandTitle(command as AdapterCommands)}</h5>
              {fileDict[command as AdapterCommands].length > 0 ? (
                <CommandFileWrapper>
                  <CodeFileIcon size={20} />{' '}
                  {fileDict[command as AdapterCommands]}
                </CommandFileWrapper>
              ) : (
                <CommandInputWrapper value={adapter[command]} disabled />
              )}
            </CommandWrapper>
          );
        })}
      </AllCommandWrap>
    );
  };
}

export default AdapterConfig;



