/**
 *
 * NotificationText
 *
 */

import React from 'react';
import styled from 'styled-components';
import Wrapper from 'components/NotificationWrapper';
import withStyledProps from 'cb-utils/withStyledProps';

interface TextWrapperProps {
  animate: boolean;
}

// note: this animation sucks
const TextWrapper = withStyledProps<TextWrapperProps>()(styled.div)`
  font-size: ${(props) => (props.animate ? '135%' : 'inherit')};
  color: ${(props) => (props.animate ? 'red' : 'inherit')};
  transition: all 3s;
`;

class NotificationText extends Wrapper<{}> {
  // eslint-disable-line react/prefer-stateless-function
  render() {
    return (
      <TextWrapper animate={this.state.hasNewValue}>
        {this.props.children}
      </TextWrapper>
    );
  }
}

export default NotificationText;



