/**
 *
 * CreateItemModal
 *
 */

import React from 'react';
import styled from 'styled-components';

import {
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Form,
  FormGroup,
  Label,
  Col,
  Input,
  ButtonGroup,
  Button,
} from 'reactstrap';

import { Row } from 'components/CrudGrid';
import { TableColumn } from 'containers/Console/state';

import generateRandomToken from 'cb-utils/generateRandomToken';
import { AnyMap } from 'cb-utils/console-entity-models';

const nameColumn = 'name';

const ModalWrapper = styled(Modal)`
  max-width: 700px !important;
`;

const LabelWrapper = styled(Label)`
  font-weight: bold;
`;

type HTMLElementEvent<T extends HTMLElement> = Event & {
  target: T;
};

interface CreateItemModalProps {
  onDismiss: () => void;
  addEventCallback: (r: Row) => void;
  columns: TableColumn[];
  addItemSuccess: boolean;
  resetCallbackStatus: () => void;
}

interface CreateItemModalState {
  newItem: {
    data: AnyMap;
  };
}

class CreateItemModal extends React.Component<
  CreateItemModalProps,
  CreateItemModalState
> {
  state: CreateItemModalState = {
    newItem: {
      data: {},
    },
  };

  componentWillMount() {
    if (this.props.columns) {
      const newItem = this.props.columns.reduce(
        (cols, col) => {
          if (col.ColumnName === 'token') {
            cols[col.ColumnName] = generateRandomToken();
          } else if (
            col.ColumnName === 'allow_key_auth' ||
            col.ColumnName === 'allow_certificate_auth'
          ) {
            cols[col.ColumnName] = true;
          } else if (col.ColumnType === 'bool') {
            cols[col.ColumnName] = false;
          } else {
            cols[col.ColumnName] = '';
          }
          return cols;
        },
        {} as AnyMap,
      );
      this.setState({
        newItem: {
          data: newItem,
        },
      });
    }
  }

  componentWillReceiveProps(nextProps: CreateItemModalProps) {
    if (nextProps.addItemSuccess) {
      this.props.onDismiss();
      this.props.resetCallbackStatus();
    }
  }

  _renderInputFields = (columns: TableColumn[]) => {
    return (
      columns
        // only show update allowed or custom fields
        .filter((col) => col.showInCreateModal || col.custom)
        // put required fields first
        .sort((a, b) => {
          if (a.ColumnName === nameColumn) {
            return -1;
          } else if (b.ColumnName === nameColumn) {
            return 1;
          }
          return !!a.requiredInCreateModal && !!b.requiredInCreateModal
            ? 0
            : a.requiredInCreateModal ? -1 : 1;
        })
        .map((column) => {
          const name = column.ColumnName;
          return (
            <FormGroup row key={`uniqueid${name}`}>
              <LabelWrapper md={5}>
                {name}:
                {column.requiredInCreateModal && (
                  <span style={{ fontWeight: 'normal' }} className="text-muted">
                    {' '}
                    (required)
                  </span>
                )}
              </LabelWrapper>
              <Col md={7}>
                {column.ColumnType === 'bool' ? (
                  <ButtonGroup>
                    <Button
                      outline
                      color="primary"
                      onClick={() => this._updateBooleanField(name, true)}
                      active={this.state.newItem.data[name]}
                    >
                      Yes
                    </Button>{' '}
                    <Button
                      outline
                      color="primary"
                      onClick={() => this._updateBooleanField(name, false)}
                      active={!this.state.newItem.data[name]}
                    >
                      No
                    </Button>{' '}
                  </ButtonGroup>
                ) : column.ColumnName === 'token' ? (
                  <Input readOnly value={this.state.newItem.data[name]} />
                ) : (
                  <Input
                    type={
                      column.ColumnType === 'string' && column.maskContents
                        ? 'password'
                        : this._determineType(column.ColumnType)
                    }
                    onChange={this._updateTextField.bind(this, name)}
                    value={this.state.newItem.data[name]}
                  />
                )}
              </Col>
            </FormGroup>
          );
        })
    );
  };

  _getResourceType = (columns: TableColumn[]) => {
    if (columns.find((col) => col.ColumnName === 'edge_key')) {
      return 'Edge';
    } else if (columns.find((col) => col.ColumnName === 'device_key')) {
      return 'Device';
    } else {
      return 'Item';
    }
  };

  _determineType = (colType: string) => {
    switch (colType) {
      case 'string':
        return 'text';
      case 'int':
      case 'bigint':
      case 'float':
      case 'double':
        return 'number';
      default:
        return 'text';
    }
  };

  _updateBooleanField = (name: string, value: boolean) => {
    this.setState({
      newItem: {
        data: {
          ...this.state.newItem.data,
          [name]: value,
        },
      },
    });
  };

  _updateTextField = (
    name: string,
    e: HTMLElementEvent<HTMLTextAreaElement>,
  ) => {
    this.setState({
      newItem: {
        data: {
          ...this.state.newItem.data,
          [name]: e.target.value,
        },
      },
    });
  };

  _isFormComplete = (columns: TableColumn[]) => {
    const requiredCols = columns
      .filter((col) => col.requiredInCreateModal && col.ColumnType !== 'bool')
      .map((column) => column.ColumnName);
    if (requiredCols.length > 0) {
      for (const col of requiredCols) {
        if (
          this.state.newItem.data.hasOwnProperty(col) &&
          this.state.newItem.data[col].length < 1
        ) {
          return true;
        }
      }
    }
    return false;
  };

  _submitNewItem = () => {
    this.props.addEventCallback(this.state.newItem);
  };

  render() {
    const { onDismiss, columns } = this.props;
    const resourceType = this._getResourceType(columns);
    return (
      // add the noDrag className so that we can type in this modal while using the CRUD grid in a portal that is in edit mode
      <ModalWrapper isOpen toggle={onDismiss} className="noDrag">
        <ModalHeader>Create New {resourceType}</ModalHeader>

        {this.state.newItem ? (
          <ModalBody>
            <Form>{this._renderInputFields(columns)}</Form>
          </ModalBody>
        ) : (
          'No columns available'
        )}

        <ModalFooter>
          <Button color="default" onClick={onDismiss}>
            Cancel
          </Button>
          <Button
            autoFocus
            color="primary"
            onClick={this._submitNewItem}
            disabled={this._isFormComplete(columns)}
          >
            Submit
          </Button>
        </ModalFooter>
      </ModalWrapper>
    );
  }
}

export default CreateItemModal;



