/**
 *
 * ConditionBuilder
 *
 */

import React, { ReactElement } from 'react';
import styled from 'styled-components';

// import { FormattedMessage } from 'react-intl';
// import messages from './messages';

import QueryBuilder from 'react-querybuilder';

import { Input } from 'reactstrap';
import {
  Condition,
  AND_OPERATOR,
  OR_OPERATOR,
} from 'cb-utils/console-entity-models/FilterCondition';
import { Map } from 'cb-utils/console-entity-models';

const InputWrapper = styled(Input)`
  && {
    display: inline;
    width: 20%;
  }
`;

const OperatorWrapper = styled(InputWrapper)`
  && {
    width: 45px;
  }
`;

const QueryBuilderWrapper = styled.div`
  .rule .form-control {
    margin-right: 0.25em;
  }

  .rule,
  .ruleGroup .ruleGroup {
    margin-top: 0.5rem;
    margin-left: 0.5rem;
  }

  .ruleGroup {
    padding: 0.5rem;
    border: 1px solid #ccc;
    border-radius: 4px;
    background: rgba(255, 233, 172, 0.5);
  }
`;

enum QueryMethods {
  EQUAL_TO = 'equalTo',
  NOT_EQUAL_TO = 'notEqualTo',
  LESS_THAN = 'lessThan',
  GREATER_THAN = 'greaterThan',
  LESS_THAN_EQUAL_TO = 'lessThanEqualTo',
  GREATER_THAN_EQUAL_TO = 'greaterThanEqualTo',
}

interface IQueryMethodInfo {
  name: string;
  method: QueryMethods;
}

export const operatorToMethodName: Map<IQueryMethodInfo> = {
  '==': {
    name: '==',
    method: QueryMethods.EQUAL_TO,
  },
  '!=': {
    name: '!=',
    method: QueryMethods.NOT_EQUAL_TO,
  },
  '<': {
    name: '<',
    method: QueryMethods.LESS_THAN,
  },
  '>': {
    name: '>',
    method: QueryMethods.GREATER_THAN,
  },
  '<=': {
    name: '<=',
    method: QueryMethods.LESS_THAN_EQUAL_TO,
  },
  '>=': {
    name: '>=',
    method: QueryMethods.GREATER_THAN_EQUAL_TO,
  },
};

const operators = [
  {
    name: operatorToMethodName['=='].name,
    label: operatorToMethodName['=='].name,
  },
  {
    name: operatorToMethodName['!='].name,
    label: operatorToMethodName['!='].name,
  },
  {
    name: operatorToMethodName['<'].name,
    label: operatorToMethodName['<'].name,
  },
  {
    name: operatorToMethodName['>'].name,
    label: operatorToMethodName['>'].name,
  },
  {
    name: operatorToMethodName['<='].name,
    label: operatorToMethodName['<='].name,
  },
  {
    name: operatorToMethodName['>='].name,
    label: operatorToMethodName['>='].name,
  },
];

const combinators = [
  { name: AND_OPERATOR, label: 'AND' },
  { name: OR_OPERATOR, label: 'OR' },
];

export interface Field {
  name: string;
  label: string;
}

interface IQueryBuilderBaseProps {
  handleOnChange: (val: string) => void;
}

export interface IQueryBuilderFieldProps extends IQueryBuilderBaseProps {
  value: string;
}

interface IOperatorOption {
  name: string;
  label: string;
}

interface IQueryBuilderOperatorProps extends IQueryBuilderBaseProps {
  value: string;
  options: IOperatorOption[];
}

export interface IQueryBuilderValueEditorProps extends IQueryBuilderBaseProps {
  value: string;
  field: string;
  operator: string;
}

interface IQueryBuilderLevelableProps {
  level: number;
}

interface IQueryBuilderAddGroupActionProps extends IQueryBuilderLevelableProps {
  handleOnClick: () => void;
}

interface IQueryBuilderCombinatorSelectorProps
  extends IQueryBuilderLevelableProps,
    IQueryBuilderBaseProps {
  value: string;
}

export interface ConditionBuilderProps {
  onChange: (cond: Condition) => void;
  condition: Condition;
  fields?: Field[];
  useCustomFields?: boolean;
  maxGroupLevel?: number;
  getFieldSelector?: (props: IQueryBuilderFieldProps) => ReactElement<any>;
  getValueEditor?: (props: IQueryBuilderValueEditorProps) => void;
}

interface ControlElements {
  operatorSelector: (props: {}) => ReactElement<any>;
  valueEditor: (props: {}) => ReactElement<any>;
  addGroupAction: (props: {}) => ReactElement<any>;
  combinatorSelector: (props: {}) => ReactElement<any>;
  fieldSelector?: (props: {}) => ReactElement<any>;
}

class ConditionBuilder extends React.PureComponent<ConditionBuilderProps, {}> {
  // eslint-disable-line react/prefer-stateless-function

  static defaultProps: Partial<ConditionBuilderProps> = {
    fields: [{ name: '', label: '' }],
    useCustomFields: false,
    maxGroupLevel: Infinity,
    condition: {
      id: 'fakeId',
      rules: [],
      combinator: 'and',
    },
  };

  constructor(props: ConditionBuilderProps) {
    super(props);
    if (!props.useCustomFields) {
      this.controlElements.fieldSelector = this.props.getFieldSelector
        ? this.props.getFieldSelector
        : this._getFieldSelector;
    }
  }

  controlElements: ControlElements = {
    operatorSelector: this.getOperatorSelector,
    valueEditor: this.props.getValueEditor
      ? this.props.getValueEditor.bind(this)
      : this.getValueEditor.bind(this),
    addGroupAction: this.getAddGroupAction.bind(this),
    combinatorSelector: this.getCombinatorSelector.bind(this),
  };

  render() {
    const { onChange, condition, fields } = this.props;
    return (
      <QueryBuilderWrapper>
        <QueryBuilder
          fields={fields}
          operators={operators}
          combinators={combinators}
          onQueryChange={onChange}
          query={condition}
          controlElements={this.controlElements}
        />
      </QueryBuilderWrapper>
    );
  }

  _getFieldSelector = (props: IQueryBuilderFieldProps) => {
    return (
      <InputWrapper
        type="text"
        value={props.value}
        onChange={(e) => props.handleOnChange(e.target.value)}
        placeholder="Variable"
      />
    );
  };

  getOperatorSelector(props: IQueryBuilderOperatorProps) {
    return (
      <OperatorWrapper
        type="select"
        value={props.value}
        onChange={(e) => props.handleOnChange(e.target.value)}
      >
        {props.options.map((option) => {
          return (
            <option key={option.name} value={option.name}>
              {option.label}
            </option>
          );
        })}
      </OperatorWrapper>
    );
  }

  getValueEditor(props: IQueryBuilderValueEditorProps) {
    return (
      <InputWrapper
        type="text"
        value={props.value}
        onChange={(e) => props.handleOnChange(e.target.value)}
        placeholder="Value"
      />
    );
  }

  getAddGroupAction(props: IQueryBuilderAddGroupActionProps) {
    if (
      props.level === this.props.maxGroupLevel ||
      (this.props.maxGroupLevel !== Infinity &&
        this.props.condition.combinator === AND_OPERATOR)
    ) {
      return null;
    }
    return (
      <button onClick={props.handleOnClick} className="ruleGroup-addGroup">
        +Group
      </button>
    );
  }

  getCombinatorSelector(props: IQueryBuilderCombinatorSelectorProps) {
    return (
      <select
        onChange={(e) => props.handleOnChange(e.target.value)}
        className="ruleGroup-combinators"
        value={props.value}
      >
        {combinators
          .filter((c) => {
            if (props.level === this.props.maxGroupLevel) {
              return c.name !== OR_OPERATOR;
            }
            return true;
          })
          .map((c) => {
            return (
              <option key={c.name} value={c.name}>
                {c.label}
              </option>
            );
          })}
      </select>
    );
  }
}

export default ConditionBuilder;



