/*
 *
 * AdapterDetail actions
 *
 */

import { BaseAction } from 'cb-utils/baseAction';
import { ActionConstants } from './actionConstants';
import { AdapterFileModel, AdapterModel } from 'cb-utils/console-entity-models';

export interface DefaultAction extends BaseAction {
  type: ActionConstants.DEFAULT_ACTION;
}

export const createDefaultAction = (): DefaultAction => ({
  type: ActionConstants.DEFAULT_ACTION,
});

export interface FetchAdapterFiles extends BaseAction {
  type: ActionConstants.FETCH_ADAPTER_FILES;
  payload: {
    sysKey: string;
    name: string;
  };
}

export const createFetchAdapterFiles = (
  sysKey: string,
  name: string,
): FetchAdapterFiles => ({
  type: ActionConstants.FETCH_ADAPTER_FILES,
  payload: {
    sysKey,
    name,
  },
});

export interface SetAdapterFiles extends BaseAction {
  type: ActionConstants.SET_ADAPTER_FILES;
  payload: {
    files?: AdapterFileModel[];
  };
}

export const createSetAdapterFiles = (
  files: AdapterFileModel[],
): SetAdapterFiles => ({
  type: ActionConstants.SET_ADAPTER_FILES,
  payload: {
    files,
  },
});

export interface ShowConfigModal extends BaseAction {
  type: ActionConstants.SHOW_CONFIG_MODAL;
}

export const createShowAdapterConfigModal = (): ShowConfigModal => ({
  type: ActionConstants.SHOW_CONFIG_MODAL,
});

export interface DismissConfigModal extends BaseAction {
  type: ActionConstants.DISMISS_CONFIG_MODAL;
}

export const createDismissAdapterConfigModal = (): DismissConfigModal => ({
  type: ActionConstants.DISMISS_CONFIG_MODAL,
});

export interface HideConfigModal extends BaseAction {
  type: ActionConstants.HIDE_CONFIG_MODAL;
}

export const createHideAdapterConfigModal = (): HideConfigModal => ({
  type: ActionConstants.HIDE_CONFIG_MODAL,
});

export interface UpdateAdapterInfo extends BaseAction {
  type: ActionConstants.UPDATE_ADAPTER_INFO;
  payload: {
    sysKey: string;
    name: string;
    adapter: AdapterModel;
  };
}

export const createUpdateAdapterInfo = (
  sysKey: string,
  name: string,
  adapter: AdapterModel,
) => ({
  type: ActionConstants.UPDATE_ADAPTER_INFO,
  payload: {
    sysKey,
    name,
    adapter,
  },
});

export interface UpdateAdapterInfoSuccess extends BaseAction {
  type: ActionConstants.UPDATE_ADAPTER_INFO_SUCCESS;
}

export const createUpdateAdapterInfoSuccess = (): UpdateAdapterInfoSuccess => ({
  type: ActionConstants.UPDATE_ADAPTER_INFO_SUCCESS,
});

export interface UpdateAdapterInfoError extends BaseAction {
  type: ActionConstants.UPDATE_ADAPTER_INFO_ERROR;
}

export const createUpdateAdapterInfoError = (): UpdateAdapterInfoError => ({
  type: ActionConstants.UPDATE_ADAPTER_INFO_ERROR,
});

export type ActionTypes =
  | DefaultAction
  | ShowConfigModal
  | DismissConfigModal
  | SetAdapterFiles
  | HideConfigModal
  | UpdateAdapterInfo
  | UpdateAdapterInfoSuccess
  | UpdateAdapterInfoError;



