/*
 *
 * AdapterEdgeManager
 *
 */

import React from 'react';
import { connect } from 'react-redux';
import styled from 'styled-components';
import { FormattedMessage, injectIntl } from 'react-intl';
import injectSaga from 'utils/injectSaga';
import { compose } from 'redux';
import injectReducer from 'utils/injectReducer';
import {
  makeSelectCurrentTab,
  makeSelectSelectedEdges,
  makeSelectDisconnectedEdges,
  makeSelectRunningEdges,
  makeSelectStoppedEdges,
  makeSelectShowLogModal,
  makeSelectLogs,
  makeSelectLoggedEdge,
  makeSelectIsFetchingAdapters,
  makeSelectIsCallingCommand,
} from './selectors';
import reducer from './reducer';
import { STORE_KEY } from './constants';
import saga from './sagas';
import messages from './messages';
import { edgeSelectors } from 'containers/Console/ducks';
import {
  createSelectNewTab,
  createFetchEdgesForAdapterRequest,
  createCallAdapterCommandRequest,
  createToggleEdgeItem,
  createShowLogModal,
  createHideLogModal,
  createFetchLogsRequest,
} from './actions';
import { Column } from 'react-table';
import Tabs from 'components/Tabs';
import { PlayIcon, StopIcon, ResetIcon } from 'components/Icons';
import withStyledProps from 'cb-utils/withStyledProps';
import { convertEntityMapToTableColumnList } from 'cb-utils/console-entity-models/convertEntityMapToArray';
import CbCrudGrid from 'components/CrudGridContainer';
import AdapterLogModal from 'components/AdapterLogModal';
import styleVars from 'cssOverrides/styleVars';
import { CBDispatch } from 'cb-utils/typeUtils';
import { createStructuredSelector } from 'reselect';

const ButtonsWrapper = styled.div`
  display: flex;
  margin-bottom: 20px;
`;

const ViewLogWrapper = styled.a`
  text-align: center;
`;

const PlayWrapper = withStyledProps<Partial<StateToProps>>()(styled.div)`
  padding: 10px;
  border: 1px solid ${styleVars.gray600};
  border-radius: 5px;
  color: ${(props) =>
    props.currentTab === 'Stopped' && props.selectedEdges.length > 0
      ? styleVars.primary
      : styleVars.gray600};
  cursor: ${(props) =>
    props.currentTab === 'Stopped' && props.selectedEdges.length > 0
      ? 'pointer'
      : 'auto'};
  pointer-events: ${(props) =>
    props.currentTab === 'Stopped' && props.selectedEdges.length > 0
      ? 'auto'
      : 'none'};
`;

const StopWrapper = withStyledProps<Partial<StateToProps>>()(styled.div)`
  padding: 10px;
  border: 1px solid ${styleVars.gray600};
  border-radius: 5px;
  color: ${(props) =>
    props.currentTab === 'Running' && props.selectedEdges.length > 0
      ? styleVars.primary
      : styleVars.gray600};
  cursor: ${(props) =>
    props.currentTab === 'Running' && props.selectedEdges.length > 0
      ? 'pointer'
      : 'auto'};
  pointer-events: ${(props) =>
    props.currentTab === 'Running' && props.selectedEdges.length > 0
      ? 'auto'
      : 'none'};
`;

const ResetWrapper = withStyledProps<Partial<StateToProps>>()(styled.div)`
  padding: 10px;
  border: 1px solid ${styleVars.gray600};
  border-radius: 5px;
  color: ${(props) =>
    props.currentTab === 'Running' && props.selectedEdges.length > 0
      ? styleVars.primary
      : styleVars.gray600};
  cursor: ${(props) =>
    props.currentTab === 'Running' && props.selectedEdges.length > 0
      ? 'pointer'
      : 'auto'};
  pointer-events: ${(props) =>
    props.currentTab === 'Running' && props.selectedEdges.length > 0
      ? 'auto'
      : 'none'};
`;

const SelectedWrapper = styled.div`
  color: ${styleVars.gray600};
  margin-top: 15px;
  margin-left: 20px;
`;

interface AdapterEdgeManagerProps {
  systemKey: string;
  adapterName: string;
}

export class AdapterEdgeManager extends React.PureComponent<
  AdapterEdgeManagerProps &
    StateToProps &
    DispatchToProps &
    ReactIntl.InjectedIntlProps,
  {}
> {
  // eslint-disable-line react/prefer-stateless-function
  render() {
    return (
      <div>
        {this.props.showLogModal && (
          <AdapterLogModal
            edgeName={this.props.loggedEdge}
            toggle={this.props.hideLogModal}
            logs={this.props.logs}
            refreshLogs={this.props.refreshLogs}
          />
        )}
        <h4>
          <FormattedMessage {...messages.header} />
        </h4>
        {this.renderCommandButtons()}
        <Tabs
          currentTab={this.props.currentTab}
          selectNewTab={this.props.selectNewTab}
          tabs={this.renderTabs()}
        />
      </div>
    );
  }

  renderCommandButtons = () => {
    return (
      <ButtonsWrapper>
        <PlayWrapper
          currentTab={this.props.currentTab}
          selectedEdges={this.props.selectedEdges}
          onClick={() => this.props.callCommand('start', this.props.currentTab)}
        >
          <PlayIcon size={30} />
        </PlayWrapper>
        <StopWrapper
          currentTab={this.props.currentTab}
          selectedEdges={this.props.selectedEdges}
          onClick={() => this.props.callCommand('stop', this.props.currentTab)}
        >
          <StopIcon size={30} />
        </StopWrapper>
        <ResetWrapper
          currentTab={this.props.currentTab}
          selectedEdges={this.props.selectedEdges}
          onClick={() =>
            this.props.callCommand('restart', this.props.currentTab)
          }
        >
          <ResetIcon size={30} />
        </ResetWrapper>
        <SelectedWrapper>
          <div className="text-muted">
            {this.props.isCallingCommand.length > 0
              ? this.props.intl.formatMessage(messages.callingCommand, {
                  command: this.props.isCallingCommand,
                  numEdges: this.props.selectedEdges.length,
                })
              : this.props.isFetchingAdapters
                ? this.props.intl.formatMessage(messages.fetchingAdapters)
                : `${this.props.intl.formatMessage(messages.edgesSelected, {
                    number: this.props.selectedEdges.length,
                  })}`}
          </div>
        </SelectedWrapper>
      </ButtonsWrapper>
    );
  };

  renderTabs = () => {
    const tabs = [
      {
        id: 'Running',
        title: `Running (${this.props.runningEdges.length})`,
        component:
          this.props.currentTab === 'Running' ? (
            <CbCrudGrid
              allowSelect
              columnNames={['name', 'logs', 'description'].concat(
                Object.keys(this.props.customColumns),
              )}
              columns={convertEntityMapToTableColumnList(
                this.props.mapOfColumns,
              )}
              transformColumns={(cols: Column[]) => {
                return this.transformColumns(cols);
              }}
              isLoading={
                this.props.isFetchingAdapters ||
                this.props.isCallingCommand.length > 0
              }
              rows={this.props.runningEdges}
              count={this.props.runningEdges.length}
              refresh={this.fetchData}
              onFetchData={this.fetchData}
              allowSort
              allowFilter
              pkColumn={this.props.PKColumnName}
              onSelectRow={this.props.toggleEdgeItem}
              selectedItems={this.props.selectedEdges}
            />
          ) : null,
      },
      {
        id: 'Stopped',
        title: `Stopped (${this.props.stoppedEdges.length})`,
        component:
          this.props.currentTab === 'Stopped' ? (
            <CbCrudGrid
              allowSelect
              columnNames={['name', 'logs', 'description'].concat(
                Object.keys(this.props.customColumns),
              )}
              columns={convertEntityMapToTableColumnList(
                this.props.mapOfColumns,
              )}
              transformColumns={(cols: Column[]) => {
                return this.transformColumns(cols);
              }}
              isLoading={
                this.props.isFetchingAdapters ||
                this.props.isCallingCommand.length > 0
              }
              rows={this.props.stoppedEdges}
              count={this.props.stoppedEdges.length}
              refresh={this.fetchData}
              onFetchData={this.fetchData}
              allowSort
              allowFilter
              pkColumn={this.props.PKColumnName}
              onSelectRow={this.props.toggleEdgeItem}
              selectedItems={this.props.selectedEdges}
            />
          ) : null,
      },
      {
        id: 'Disconnected',
        title: `Disconnected Edges (${this.props.disconnectedEdges.length})`,
        component:
          this.props.currentTab === 'Disconnected' ? (
            <CbCrudGrid
              allowSelect
              columnNames={['name', 'logs', 'description'].concat(
                Object.keys(this.props.customColumns),
              )}
              columns={convertEntityMapToTableColumnList(
                this.props.mapOfColumns,
              )}
              transformColumns={(cols: Column[]) => {
                return this.transformColumns(cols);
              }}
              isLoading={
                this.props.isFetchingAdapters ||
                this.props.isCallingCommand.length > 0
              }
              rows={this.props.disconnectedEdges}
              count={this.props.disconnectedEdges.length}
              refresh={this.fetchData}
              onFetchData={this.fetchData}
              allowSort
              allowFilter
              pkColumn={this.props.PKColumnName}
              onSelectRow={this.props.toggleEdgeItem}
              selectedItems={this.props.selectedEdges}
            />
          ) : null,
      },
    ];
    return tabs;
  };

  transformColumns = (cols: Column[]) => {
    const columns = cols.map((col) => {
      const colName = col.accessor;
      switch (colName) {
        case 'logs':
          col.Header = colName;
          col.Cell = (cellInfo) => {
            return (
              <ViewLogWrapper
                href=""
                id={cellInfo.row.name}
                onClick={(e) => {
                  e.preventDefault();
                  this.props.displayLogModal(cellInfo.row.name);
                }}
              >
                View
              </ViewLogWrapper>
            );
          };
        default:
          return col;
      }
    });
    return columns;
  };

  fetchData = (query?: QueryObj) => {
    this.props.fetchEdgesForAdapter(this.props.currentTab, query);
  };
}

type StateToProps = ReturnType<typeof mapStateToProps>;

const mapStateToProps = createStructuredSelector({
  currentTab: makeSelectCurrentTab(),
  showLogModal: makeSelectShowLogModal(),
  loggedEdge: makeSelectLoggedEdge(),
  logs: makeSelectLogs(),
  runningEdges: makeSelectRunningEdges(),
  stoppedEdges: makeSelectStoppedEdges(),
  disconnectedEdges: makeSelectDisconnectedEdges(),
  selectedEdges: makeSelectSelectedEdges(),
  isFetchingAdapters: makeSelectIsFetchingAdapters(),
  isCallingCommand: makeSelectIsCallingCommand(),
  mapOfColumns: edgeSelectors.columns(),
  PKColumnName: edgeSelectors.PKColumnName('edges'),
  customColumns: edgeSelectors.customColumns(),
});

type DispatchToProps = ReturnType<typeof mapDispatchToProps>;
function mapDispatchToProps(
  dispatch: CBDispatch,
  ownProps: AdapterEdgeManagerProps,
) {
  return {
    fetchEdgesForAdapter: (status: string, query?: QueryObj) =>
      dispatch(
        createFetchEdgesForAdapterRequest(ownProps.adapterName, status, query),
      ),
    selectNewTab: (status: string) => dispatch(createSelectNewTab(status)),
    callCommand: (command: string, status: string) =>
      dispatch(
        createCallAdapterCommandRequest(ownProps.adapterName, command, status),
      ),
    toggleEdgeItem: (selectedEdges: string[]) =>
      dispatch(createToggleEdgeItem(selectedEdges)),
    displayLogModal: (edgeName: string) =>
      dispatch(createShowLogModal(ownProps.adapterName, edgeName)),
    hideLogModal: () => dispatch(createHideLogModal()),
    refreshLogs: (edgeName: string) =>
      dispatch(createFetchLogsRequest(ownProps.adapterName, edgeName)),
  };
}

const withConnect = connect(mapStateToProps, mapDispatchToProps);

const withReducer = injectReducer({ key: STORE_KEY, reducer });
const withSaga = injectSaga({ key: STORE_KEY, saga });
export default compose(withReducer, withSaga, withConnect, injectIntl)(
  AdapterEdgeManager,
) as React.ComponentType<AdapterEdgeManagerProps>;



