import { ColumnType } from 'cb-utils/console-entity-models';

function convertStringToType(value: string, toType: ColumnType) {
  if (typeof value !== 'string') {
    return value;
  }
  let fmtVal;
  switch (toType) {
    case ColumnType.uuid:
      fmtVal = value;
      if (value === '') {
        fmtVal = null;
      }
      break;
    case ColumnType.string:
      fmtVal = value;
      break;
    case ColumnType.int:
      fmtVal = Number.parseInt(value, 10);
      break;
    case ColumnType.bigint:
      fmtVal = Number.parseInt(value, 10);
      break;
    case ColumnType.float:
      fmtVal = Number.parseFloat(value);
      break;
    case ColumnType.double:
      fmtVal = Number.parseFloat(value);
      break;
    case ColumnType.timestamp:
      fmtVal = value;
      break;
    case ColumnType.bool:
      if (value === 'false') {
        fmtVal = false;
      } else if (value === '') {
        fmtVal = null;
      } else {
        fmtVal = true;
      }
      break;
    case ColumnType.blob:
      fmtVal = value;
      break;
    default:
      // tslint:disable-next-line
      console.warn('unspecified type for string conversion');
      fmtVal = value;
      break;
  }
  return fmtVal;
}

export default convertStringToType;



