import React from 'react';
import styled from 'styled-components';

import ReactTable, { FinalState, RowInfo } from 'react-table';
import 'react-table/react-table.css';
import { FormattedMessage } from 'react-intl';
// import messages from './messages';
import { createCheckBoxCell } from 'cb-utils/createCheckBoxCell';
import CrudGridPagination from 'components/CrudGridPagination';
import { AnyMap, PrimitiveType } from 'cb-utils/console-entity-models';
import { TableColumn } from 'containers/Console/state';
import styleVars from 'cssOverrides/styleVars';
import EditableCell, { RTCellInfo, RTColumn } from './EditableCell';

const SpanMuted = styled.span`
  color: ${styleVars.gray500};
`;

export interface Row {
  data: {};
}

interface CRUDgridProps {
  data: AnyMap[];
  keyColumn: string;
  columns: TableColumn[];
  columnNames?: string[];
  showColumnTypes?: boolean;
  enableSorting?: boolean;
  allowSearch?: boolean;
  allowSelect?: boolean;
  selected?: string[]; // selected is an array of IDs
  isDeleting?: boolean; // array of IDs
  selectedColumnText?: FormattedMessage.MessageDescriptor;
  selectedColumnWidth?: number;
  showPagination?: boolean;
  pageSizeOptions?: number[];
  defaultPageSize?: number;
  pageSize?: number;
  count: number;
  pages?: number;
  onFetchData?: (state: FinalState, instance: any) => void;
  loading?: boolean;
  updateEventCallback?: (
    row: RowInfo,
    col: string,
    value: PrimitiveType,
  ) => void;
  addEventCallback?: (addedData: Row) => void;
  deleteEventCallback?: (rows: string[]) => void;
  selectEventCallback?: (rows: string[]) => void;
  transformColumns?: (columns: RTColumn[]) => RTColumn[];
  noUpdateColumns?: string[];
  disablePaginationOptions?: boolean;
}

interface CRUDgridState {
  gridData: AnyMap[];
  selected: string[];
  page: number;
  pageSize: number;
  editingJson: string;
}

// TODO - Add button text customizations

export class CRUDgridWidget extends React.Component<
  CRUDgridProps,
  CRUDgridState
> {
  // eslint-disable-line react/prefer-stateless-function

  static defaultProps: Partial<CRUDgridProps> = {
    allowSelect: false,
    enableSorting: false,
    allowSearch: false,
    loading: false,
    selected: [],
    isDeleting: false,
    selectedColumnText: null,
    selectedColumnWidth: 40,
    updateEventCallback: null,
    noUpdateColumns: [],
    showColumnTypes: true,
    disablePaginationOptions: false,
  };

  state: CRUDgridState = {
    gridData: [...this.props.data] || [],
    selected: [],
    page: 0,
    pageSize: this.props.pageSize,
    editingJson: '',
  };

  grid: FinalState = null;
  currentRowId: string = null; // refers to the ID of the row the the user is editing

  componentWillReceiveProps(nextProps: CRUDgridProps) {
    // keeps table rows in place while editing due to weird rearrangement on backend
    const newData = nextProps.data ? nextProps.data : [];
    this.setState({
      gridData: newData,
    });
  }

  checkForExtraColumns = (loopedRow: AnyMap, otherRow: AnyMap) => {
    const keys = Object.keys(loopedRow);
    for (const key of keys) {
      if (otherRow[key] === undefined) {
        return true;
      }
    }
    return false;
  };

  checkForUpdateCellValues = (
    newData: AnyMap[],
    curData: AnyMap[],
    keyCol: string,
  ) => {
    const cols = Object.keys(newData[0]);
    for (const d of newData) {
      const currentRow = curData.find((row) => {
        return row[keyCol] === d[keyCol];
      });
      if (!currentRow) {
        return true;
      }
      for (const col of cols) {
        if (d[col] && currentRow[col] && d[col] !== currentRow[col]) {
          return true;
        }
      }
    }
    return false;
  };

  checkForNewTable = (newData: AnyMap[], curData: AnyMap[], keyCol: string) => {
    for (const d of newData) {
      const currentId = d[keyCol];
      if (curData.map((row) => row[keyCol]).indexOf(currentId) > -1) {
        return false;
      }
    }
    return true;
  };

  findNewRow = (newData: AnyMap[], curData: AnyMap[], keyCol: string) => {
    for (const d of newData) {
      const currentId = d[keyCol];
      if (curData.map((row) => row[keyCol]).indexOf(currentId) === -1) {
        return d;
      }
    }
  };

  render() {
    const theGridData = this.state.gridData;
    const {
      showPagination,
      loading,
      allowSelect,
      pageSize,
      defaultPageSize,
      pageSizeOptions,
      updateEventCallback,
    } = this.props;
    return (
      <ReactTable
        // @ts-ignore (ignore until react-table fixes their fetchData type definition)
        manual
        PaginationComponent={this._createPaginationComponent}
        data={theGridData}
        sortable={this.props.enableSorting}
        defaultPageSize={defaultPageSize}
        pageSize={pageSize}
        pageSizeOptions={pageSizeOptions}
        showPagination={showPagination}
        onFetchData={this._onFetchData}
        filterable={this.props.allowSearch}
        pages={this._getNumberOfPages()}
        loading={loading}
        defaultSorted={
          this.props.keyColumn // if pkColumn is determined by column fetch, may be undefined on first render
            ? [{ id: this.props.keyColumn, desc: false }]
            : []
        }
        columns={this._createGridColumns(
          theGridData,
          !!updateEventCallback,
          allowSelect,
        )}
      />
    );
  }

  _createPaginationComponent = (state: FinalState) => {
    return (
      <CrudGridPagination
        gridState={state}
        total={this.props.count}
        disablePaginationOptions={this.props.disablePaginationOptions}
      />
    );
  };

  _onFetchData = (state: FinalState, instance: any) => {
    this.grid = state;
    this.props.onFetchData(state, instance);
  };

  _getNumberOfPages = (): number => {
    if (this.grid) {
      return Math.ceil(this.props.count / this.grid.pageSize);
    }
    return 0;
  };

  _createGridColumns = (
    griddata: AnyMap[],
    allowEdits: boolean,
    allowSelect: boolean,
  ) => {
    const columns: RTColumn[] = [];
    if (allowSelect) {
      const checkmarkCol = {
        Header: () =>
          createCheckBoxCell(
            this._selectAll,
            this.props.selected.length === this.state.gridData.length,
            this.props.selectedColumnText,
          ),
        filterable: false,
        resizable: false,
        width: this.props.selectedColumnWidth,
        Cell: (row: any) => {
          return createCheckBoxCell(
            (isChecked) => this._toggleRowSelection(isChecked, row),
            this._isRowSelected(row),
          );
        },
      };
      columns.push(checkmarkCol);
    }
    const columnNames =
      this.props.columnNames || (griddata[0] ? Object.keys(griddata[0]) : []);
    columnNames.forEach((colName) => {
      const column = this.props.columns.find((c) => c.ColumnName === colName);
      const col: RTColumn = {
        Header: (headerInfo) => {
          return (
            <div>
              {colName}{' '}
              {this.props.showColumnTypes && (
                <SpanMuted>{column && column.ColumnType}</SpanMuted>
              )}
            </div>
          );
        },
        accessor: colName,
        columnData: column,
      };
      col.Cell = this._renderCell;
      columns.push(col);
    });
    if (this.props.transformColumns) {
      return this.props.transformColumns(columns);
    }
    return columns;
  };

  _isRowSelected = (rowInfo: RowInfo): boolean => {
    return this.props.selected.indexOf(rowInfo.row[this.props.keyColumn]) > -1;
  };

  _formatCellValue = (value: any) => {
    if (value && typeof value === 'object') {
      // mongo connection returns a nested feild for {_document { "_id": "23j4", "name": "jenny"}} which kills page with invalid react children
      // general precaution - convert values that come in as objects to strings
      return JSON.stringify(value);
    }
    return typeof value === 'boolean' ? value.toString() : value;
  };

  // note: typing this as any until react-table updates their d.ts
  _renderCell = (cellInfo: RTCellInfo) => {
    const column = cellInfo.column.columnData;
    const currentVal = this.state.gridData[cellInfo.index][cellInfo.column.id];
    if (!column) return this._formatCellValue(currentVal); // if column data hasn't been fetched yet
    return (
      <EditableCell
        noUpdates={!this.props.updateEventCallback}
        cellInfo={cellInfo}
        value={currentVal}
        column={column}
        onCellEdit={this._onCellEdit}
      />
    );
  };

  _toggleRowSelection = (isChecked: boolean, rowInfo: RowInfo) => {
    const newSelected = [...this.props.selected];
    const id = rowInfo.row[this.props.keyColumn];
    if (isChecked) {
      newSelected.push(id);
    } else {
      newSelected.splice(newSelected.indexOf(id), 1);
    }
    this.props.selectEventCallback(newSelected);
  };

  _selectAll = (isChecked: boolean) => {
    const newSelected: string[] = [];
    if (isChecked) {
      this.state.gridData.forEach((d) => {
        newSelected.push(d[this.props.keyColumn]);
      });
    }
    this.props.selectEventCallback(newSelected);
  };

  _onCellEdit = (
    row: RowInfo,
    columnName: string,
    index: number,
    cellValue: PrimitiveType,
  ) => {
    const newRow = this.state.gridData[index];
    newRow[columnName] = cellValue;
    this.currentRowId = newRow[this.props.keyColumn];
    this.setState({
      gridData: [
        ...this.state.gridData.slice(0, index),
        newRow,
        ...this.state.gridData.slice(index + 1),
      ],
    });
    this.props.updateEventCallback(row, columnName, cellValue);
  };

  _deleteRow = (rowInfo: RowInfo) => {
    this.setState({
      gridData: [
        ...this.state.gridData.slice(0, rowInfo.index),
        ...this.state.gridData.slice(rowInfo.index + 1),
      ],
    });
    this.props.deleteEventCallback(rowInfo.original);
  };
}

export default CRUDgridWidget;



