/**
 *
 * CrudGridPagination
 *
 */

import React from 'react';
import styled from 'styled-components';

import { FormattedMessage } from 'react-intl';
import messages from './messages';
import { FinalState } from 'react-table';
import { Button, Form, Input } from 'reactstrap';

import NumberField from 'components/CbNumberInputField';
import { CaretRight, CaretLeft } from 'components/Icons';

import NotificationText from 'components/NotificationText';

const Wrapper = styled.div`
  &&& {
    padding: 0.75rem;
  }
`;

const FormWrapper = styled(Form)`
  width: 100%;
`;

const PaginationControl = styled.div`
  && > * {
    margin-right: 0.4rem;
  }
`;

interface CrudGridPaginationProps {
  gridState: FinalState;
  total: number;
  disablePaginationOptions?: boolean;
}

class CrudGridPagination extends React.PureComponent<
  CrudGridPaginationProps,
  {}
> {
  // eslint-disable-line react/prefer-stateless-function

  static defaultProps: Partial<CrudGridPaginationProps> = {
    total: 0,
    disablePaginationOptions: false,
  };

  render() {
    const {
      gridState: { pages, page, pageSizeOptions, pageSize },
      total,
    } = this.props;
    const formattedPage = page + 1;
    return (
      <Wrapper className="-pagination">
        <FormWrapper inline onSubmit={(e) => e.preventDefault()}>
          {pages > 1 && (
            <PaginationControl>
              <Button
                disabled={formattedPage === 1}
                onClick={() => this.handlePageChange(page - 1)}
                color="secondary"
              >
                <CaretLeft />
              </Button>
              <span>
                <NumberField
                  value={formattedPage}
                  min={1}
                  max={pages}
                  onChange={(e) => this.handlePageChange(e.target.value)}
                  name=""
                />
                /
                {pages}
              </span>
              <Button
                disabled={formattedPage === pages}
                onClick={() => this.handlePageChange(page + 1)}
                color="secondary"
              >
                <CaretRight />
              </Button>
              {!this.props.disablePaginationOptions && (
                <span>
                  <Input
                    type="select"
                    value={pageSize}
                    onChange={(e) => this.handlePageSizeChange(+e.target.value)}
                  >
                    {pageSizeOptions.map((p) => (
                      <option key={`ps-${p}`} value={p}>
                        {p}
                      </option>
                    ))}
                  </Input>
                  <FormattedMessage {...messages.rowsPerPage} />
                </span>
              )}
            </PaginationControl>
          )}
          <div className="ml-auto">
            <NotificationText valuetowatch={total}>
              Total: {total}
            </NotificationText>
          </div>
        </FormWrapper>
      </Wrapper>
    );
  }

  handlePageChange = (page: number) => {
    this.props.gridState.onPageChange(page);
  };

  handlePageSizeChange = (size: number) => {
    // todo: make the page param dynamic
    this.props.gridState.onPageSizeChange(size, this.props.gridState.page);
  };
}

export default CrudGridPagination;



