import React from 'react';
import { ColumnType, PrimitiveType } from 'cb-utils/console-entity-models';
import CbDatePicker from '../CbDatePicker';
import convertStringToType from 'cb-utils/convertStringToType';
import styled from 'styled-components';
import { RowInfo, Column } from 'react-table';
import { TableColumn } from 'containers/Console/state';
import { Modal } from 'reactstrap';
import JsonModal from 'components/JsonModal';
import createFakeEvent from 'cb-utils/createFakeEvent';
import { CBEvent } from 'utils/types';
import { validUuidFormat } from 'utils/validations';
import classNames from 'classnames';
import { FormattedMessage } from 'react-intl';
import messages from './messages';
import { stripExtraSpacesJson } from 'cb-utils/services/formatJson';

export interface RTColumn extends Column {
  columnData?: TableColumn;
}

export interface RTCellInfo {
  column: RTColumn;
  row: RowInfo;
  index: number;
  original: { [k: string]: PrimitiveType };
}

const Wrapper = styled.div`
  .form-control {
    border: none;
    padding: 0;
    background: none;
  }
  input {
    width: 100%;
    border: none;
  }
`;

const shared = `
  height: 100%;
  margin-top: 1px;
  padding: 0 0.5em;
`;

const SideLink = styled.button`
  ${shared};
`;
const SideError = styled.div`
  ${shared} color: red;
`;

interface IProps {
  value: PrimitiveType;
  column: TableColumn;
  noUpdates?: boolean;
  cellInfo: RTCellInfo;
  onCellEdit: (
    row: RowInfo,
    columnName: string,
    index: number,
    cellValue: PrimitiveType,
  ) => void;
}

const ENTER_CODE = 13;

class EditableCell extends React.PureComponent<IProps> {
  state = {
    value: this.props.value,
    isOpen: false,
    error: false,
  };
  componentWillReceiveProps(nextProps: IProps) {
    if (nextProps.value !== this.props.value) {
      this.setState({ value: nextProps.value });
    }
  }
  keyPress = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.keyCode === ENTER_CODE) {
      // @ts-ignore
      this.refs.myInput.blur(e);
    }
  };
  toggleOpen = () => {
    this.setState({ isOpen: !this.state.isOpen });
  };
  handleChange = ({ target }: React.ChangeEvent<HTMLInputElement>) => {
    let value = target.value;
    if (this.props.column.ColumnType === ColumnType.int) {
      // remove decimal
      value = target.value.replace(/[^0-9|-]/g, '');
    }
    this.setState({ value });
  };
  handleUpdate = (e: React.FocusEvent<HTMLInputElement> | CBEvent) => {
    // @ts-ignore - not sure why it's doesn't know it has a value property
    let currentVal = e.target.value;
    if (this.props.column.ColumnType === ColumnType.bool) {
      if (currentVal.match(/^t/)) {
        currentVal = 'true';
      } else if (currentVal.match(/^f/)) {
        currentVal = 'false';
      } else if (currentVal === '') {
        currentVal = '';
      } else {
        this.setState({ error: true });
        return;
      }
    }
    currentVal =
      typeof currentVal === 'string'
        ? convertStringToType(currentVal, this.props.column.ColumnType)
        : currentVal;

    if (this.props.column.ColumnType === ColumnType.uuid) {
      if (currentVal && !validUuidFormat(currentVal)) {
        this.setState({ error: true });
        return;
      }
    }

    const originalVal = this.props.cellInfo.original[
      this.props.column.ColumnName
    ];
    this.setState({ value: currentVal, error: false });
    // set state first so can clear an error

    if (originalVal === currentVal) {
      return;
    }

    this.props.onCellEdit(
      this.props.cellInfo.row,
      this.props.cellInfo.column.id,
      this.props.cellInfo.index,
      currentVal,
    );
  };
  render() {
    const readOnly = this.props.column.noUpdateAllowed || this.props.noUpdates;
    const sharedProps = {
      ref: 'myInput',
      onKeyDown: this.keyPress,
      'data-cy': this.props.column.ColumnName + this.props.cellInfo.index,
      disabled: readOnly,
    };
    const wrapperClassName = classNames('d-flex', readOnly && 'text-muted');
    const errorDiv = this.state.error && (
      <SideError data-cy="invalid-input">
        <FormattedMessage {...messages.invalid} />
      </SideError>
    );
    const currentVal =
      this.state.value === null || this.state.value === undefined
        ? ''
        : (this.state.value as string);
    const onCellEdit = this.handleUpdate;
    switch (this.props.column.ColumnType) {
      case ColumnType.timestamp:
        return (
          <Wrapper className={wrapperClassName}>
            <CbDatePicker
              {...sharedProps}
              onChange={onCellEdit}
              value={currentVal}
              showTime
            />
          </Wrapper>
        );
      case ColumnType.float:
      case ColumnType.bigint:
      case ColumnType.double:
      case ColumnType.int:
        return (
          <Wrapper className={wrapperClassName}>
            <input
              {...sharedProps}
              type="number"
              onChange={this.handleChange}
              onBlur={onCellEdit}
              value={currentVal}
            />
          </Wrapper>
        );
      case ColumnType.string:
        // for mongo that has objects come in for strings
        let currentValStr = currentVal;
        if (typeof currentVal !== 'string') {
          currentValStr = JSON.stringify(currentVal);
        }

        const showModal =
          currentValStr &&
          (currentValStr.length > 50 || currentValStr[0].match(/\{|\[/));
        return (
          <Wrapper className={wrapperClassName}>
            <Modal isOpen={!!this.state.isOpen} toggle={this.toggleOpen}>
              <JsonModal
                header={<div>{this.props.cellInfo.column.id}</div>}
                inputJson={currentValStr}
                onSave={(text) =>
                  this.handleUpdate(createFakeEvent(stripExtraSpacesJson(text)))
                }
                onHide={this.toggleOpen}
                readOnly={readOnly}
              />
            </Modal>
            <input
              {...sharedProps}
              onChange={this.handleChange}
              onBlur={onCellEdit}
              value={currentValStr}
            />
            {showModal && (
              <SideLink className="btn btn-link" onClick={this.toggleOpen}>
                {readOnly ? (
                  <FormattedMessage {...messages.view} />
                ) : (
                  <FormattedMessage {...messages.edit} />
                )}
              </SideLink>
            )}
          </Wrapper>
        );
      case ColumnType.uuid:
        return (
          <Wrapper className={wrapperClassName}>
            <input
              {...sharedProps}
              onChange={this.handleChange}
              onBlur={onCellEdit}
              value={currentVal}
            />
            {errorDiv}
          </Wrapper>
        );
      default:
        return (
          <Wrapper className={wrapperClassName}>
            <input
              {...sharedProps}
              onChange={this.handleChange}
              onBlur={onCellEdit}
              value={currentVal}
            />
            {errorDiv}
          </Wrapper>
        );
    }
  }
}

export default EditableCell;



