const generateRandomToken = () => {
  let token = '';
  for (
    let i = 0, varLenth = Math.floor(Math.random() * 10) + 22;
    i < varLenth;
    i+=1
  ) {
    let val: string | number = '';
    if (Math.floor(Math.random() * 10) % 2) {
      val = Math.floor(Math.random() * 10);
    } else {
      val = String.fromCharCode(Math.random() * 25 + 65);
      if (Math.floor(Math.random() * 10) % 2) {
        val = val.toLowerCase();
      }
    }
    token += val;
  }
  return token;
};

export default generateRandomToken;



