import { createSelector } from 'reselect';
import { selectEntities } from '../consoleSelectors';

const selectAdapters = () =>
  createSelector(selectEntities(), (substate) => substate.adaptors);

const selectAdapterEntities = () =>
  createSelector(selectAdapters(), (substate) => substate.entities);

const selectAdapterByName = (name: string) =>
  createSelector(selectAdapterEntities(), (substate) => {
    return substate[name];
  });

export { selectAdapterByName };



