/**
 *
 * AdapterCommandManager
 *
 */

import React from 'react';
import styled from 'styled-components';
import { Button, ButtonGroup, Row, Col, Input } from 'reactstrap';
import { IAdapterCommands } from 'cb-utils/console-entity-models';
import AdapterFileManager from 'components/AdapterFileManager';
import { FormattedMessage } from 'react-intl';
import messages from './messages';
import formatAdapterCommandTitle from 'components/AdapterConfig/utils';

const Wrapper = styled.div`
  margin-bottom: 1rem;
`;

interface AdapterCommandManagerProps {
  commandName: keyof IAdapterCommands;
  command: string;
  onCommandChange: (field: keyof IAdapterCommands, value: string) => void;
  fileName: string;
  fileType: string;
  uploadFiles: (e: any, name: string, type: string) => void;
  deleteFile: (name: string) => void;
}

interface AdapterCommandManagerState {
  command: string;
  displayFile: boolean;
}

class AdapterCommandManager extends React.Component<
  AdapterCommandManagerProps,
  AdapterCommandManagerState
> {
  state: AdapterCommandManagerState = {
    command: this.props.command,
    displayFile: this.props.fileName.length > 0,
  };

  componentWillReceiveProps(nextProps: AdapterCommandManagerProps) {
    this.setState({
      command: nextProps.command,
      displayFile: nextProps.fileName.length > 0,
    });
  }

  render() {
    const { command, displayFile } = this.state;
    const {
      commandName,
      onCommandChange,
      fileName,
      fileType,
      uploadFiles,
      deleteFile,
    } = this.props;
    return (
      <Wrapper>
        <Row>
          <Col>
            <h5>{formatAdapterCommandTitle(commandName)}</h5>
          </Col>
          <Col sm={9}>
            <ButtonGroup size="sm">
              <Button
                outline
                color="primary"
                onClick={() => this.toggleButton(false)}
                active={!displayFile}
                data-cy="adapter-command-button"
              >
                <FormattedMessage {...messages.command} />
              </Button>
              <Button
                outline
                color="primary"
                onClick={() => this.toggleButton(true)}
                active={displayFile}
              >
                <FormattedMessage {...messages.file} />
              </Button>
            </ButtonGroup>
          </Col>
        </Row>
        {displayFile ? (
          <AdapterFileManager
            fileName={fileName}
            fileType={fileType}
            uploadFiles={uploadFiles}
            deleteFile={deleteFile}
            commandName={commandName}
          />
        ) : (
          <Input
            value={command}
            onChange={(e) => this.onCommandChange(e.target.value)}
            onBlur={(e) => onCommandChange(commandName, command)}
            data-cy="adapter-command-input"
          />
        )}
      </Wrapper>
    );
  }

  onCommandChange = (value: string) => {
    this.setState({
      ...this.state,
      command: value,
    });
  };

  toggleButton(value: boolean) {
    this.setState({
      displayFile: value,
    });
  }
}

export default AdapterCommandManager;



